import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Button from '../../../../../../components/button';
import Chip from '../../../../../../components/chip';
import Text from '../../../../../../components/text';


const useStyles = makeStyles((theme) => ({
    container: {
        padding: theme.spacing(1.5),
        border: `1px solid #cecece`,
        borderRadius: 2,
    },
    photo: {
        height: 150,
        width: 240,
        borderRadius:2,
        overflow: 'hidden',
    },
    flightLine: {
        height: 1,
        width: '100%',
        backgroundColor: '#cecece',
    }
}));


function Hotel(props){
    const classes = useStyles();
    const { hotel } = props;
    return (
        <div className={classes.container}>
            <div className='r-f'>
                <div className='m-r-12'>
                    <div className={classes.photo}>
                        <img src={hotel.photo} alt={hotel.title} />
                    </div> 
                </div>
                <div className='f-g-1'>
                    <div className='r-f a-c'>
                        <div className='m-r-12 f-g-1'>
                            <Text text={hotel.title} variant='title' />
                            <Text text={hotel.location} variant='meta' />
                        </div>
                        <div>
                            <Chip 
                                label={`${hotel.rating}/5`} 
                                color='success'
                                size='small' 
                            />
                        </div>
                    </div>
                    <div className='m-t-6'>
                        <Text text={'Dates: '} variant='meta' />
                        <Text text={`${hotel.check_in} - ${hotel.check_out}`} variant='text' />
                    </div>
                    <div className='m-t-6'>
                        <div className='r-f a-c'>
                            <div className='r-f a-c m-r-12'>
                                <Text text={'Room Type: '} variant='meta' />
                                <Text text={`${hotel.room_type}`} variant='meta' />
                            </div>
                            <div className='r-f a-c'>
                                <Text text={'Includes: '} variant='meta' />
                                <Text text={`Breakfast`} variant='meta' />
                            </div>
                        </div>
                    </div>
                    <div className='r-f j-e m-t-8'>
                        <div className='f-g-1' />
                        <div className='m-l-12'>
                            <Button 
                                text='Change Room' 
                                variant='outlined' 
                                color='default'
                                size='small' 
                            />
                        </div>                
                        <div className='m-l-12'>
                            <Button 
                                text='Change' 
                                variant='outlined' 
                                color='default'
                                size='small' 
                            />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Hotel;
