import React, { useReducer, useEffect, useRef } from 'react';
import { useHistory } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import SearchIcon from '@material-ui/icons/SearchRounded';
import { DateInput, Button, AutoComplete } from '../../../../../components';
import PassengerDetails from '../flights/form/PassengerDetails';
import { CHANGE, ERROR, REMOVE_ERROR, holidayReducer, initialState } from './HolidaysReducer';


const LEAVE_OPTIONS = ['New Delhi - India', 'Thimpu Bhutan', 'Bangalore - India', 'Chicago - USA', 'Durban - South Africa', 'London - UK'];

const DESTINATION_OPTION = ['Abu Dhabi - UAE', 'Dubai - UAE', 'North India - India', 'South India - India', 'Punakha - Bhutan', 'Maputo - Mozambique', 'Paro - Bhutan', 'Nassau - The Bahamas'];


function HolidayForm(props){
    const [state, dispatch] = useReducer(holidayReducer, initialState);
    const history = useHistory();
    const { error } = state;
    const fromRef = useRef();
    const toRef = useRef();
    const departureRef = useRef();
    // const returnRef = useRef();
    const validate = () => {
        let flag = true;
        // if (!state.from) {
        //     dispatch({type: ERROR, name: 'from', value: 'Please enter a leaving'});
        //     flag = false;
        // }
        if (!state.to) {
            dispatch({type: ERROR, name: 'to', value: 'Please enter a destination'});
            flag = false;
        }
        // if (!state.departure) {
        //     dispatch({
        //         type: ERROR,
        //         name: 'departure',
        //         value: 'Please enter a departure date',
        //     });
        //     flag = false;
        // }
        // if (!state.return) {
        //     dispatch({
        //         type: ERROR,
        //         name: 'return',
        //         value: 'Please enter a return date',
        //     });
        //     flag = false;
        // }
        return flag;
    }
    const onSubmit = () => {
        if (validate()) {
            let query = {};
            query.leaving_from = state.from;
            query.destination = state.to;
            query.departure = state.departure;
            query.return = state.return;
            query.adults = state.adults;
            query.childrens = state.childrens;
            query.infants = state.infants;
            console.log(history);
            // history.push(`/search/holidays?query=${JSON.stringify(query)}`);
        }        
    }
    useEffect(() => {
        if(state.from && error.from) {
            console.log(error.from);
            dispatch({type: REMOVE_ERROR, name: 'from'});
        }
    }, [state.from, error.from]);

    useEffect(() => {
        if(state.to && error.to) {
            dispatch({type: REMOVE_ERROR, name: 'to'});
        }
    }, [state.to, error.to]);

    useEffect(() => {
        if(state.departure && error.departure) {
            dispatch({type: REMOVE_ERROR, name: 'departure'});
        }
    }, [state.departure, error.departure]);

    useEffect(() => {
        if(state.return && error.return) {
            dispatch({type: REMOVE_ERROR, name: 'return'});
        }
    }, [state.return, error.return]);

    return(
        <div>
            <div>
                <Grid container spacing={2}>
                    <Grid item xs={12} sm={6} md={3}>
                        <AutoComplete 
                            name='from'
                            value={state.from}
                            label='Leaving From'
                            onChange={
                                (event, newValue) => {
                                    dispatch({type: CHANGE, name: 'from', value: newValue});
                                }
                            }
                            error={error.from}
                            reference={fromRef}
                            options ={LEAVE_OPTIONS}
                        /> 
                    </Grid>
                    <Grid item xs={12} sm={6} md={3}>
                        <AutoComplete
                            name='to'
                            value={state.to}
                            label='Destination'
                            onChange={
                                (event, newValue) => {
                                    dispatch({type: CHANGE, name: 'to', value: newValue});
                                }
                            }
                            error={error.to}
                            reference={toRef}
                            options={DESTINATION_OPTION}
                        />
                    </Grid>
                    <Grid item xs={12} sm={6} md={3}>
                        <DateInput 
                            label='Departure Date'
                            name='departure'
                            value={state.departure}
                            onChange={(date) => dispatch({type: CHANGE, name: 'departure', value: date})}
                            error={error.departure}
                            reference={departureRef}
                        />
                    </Grid>
                    <Grid item xs={12} sm={6} md={3}>
                        <PassengerDetails 
                            state={state} 
                            dispatch={dispatch} 
                        />
                    </Grid>
                </Grid>
            </div>  
            <div className='m-t-6'>
                <Grid container spacing={2} justify='flex-end'>
                    <Grid item>
                        <Button 
                            color='primary'
                            text='Search'
                            icon={
                                <SearchIcon />
                            }
                            onClick={onSubmit}
                        />
                    </Grid>                   
                </Grid>
            </div>          
        </div>
    );
}


export default HolidayForm;