import React from 'react';
import Grid from '@material-ui/core/Grid';
import InvestCard from './InvestCard';
import LoadingCard from './LoadingCard';



function InvestCardList(props){
    let cardList = [], loadingCardList = [];
    for (let i = 0; i < (props.itemList ? props.itemList.length : 10); i++) {
        cardList.push(
            <Grid item xs={6} md={3} key={i} >
                <InvestCard item={props.itemList ? props.itemList[i] :{}} />
            </Grid>
        );
        loadingCardList.push(
            <Grid item xs={6} md={3}  key={i} >
                <LoadingCard />
            </Grid>
        ); 
    }
    return (
        <div>
            <Grid container spacing={2}>
                {
                    props.isLoading ?
                        loadingCardList
                    :
                        cardList
                }
            </Grid>
        </div>
    );
}

export default InvestCardList;
