import React, { useReducer, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import { DateInput, Button, AutoComplete } from '../../../../../../components';
import PassengerDetails from '../form/PassengerDetails';
import { CHANGE, ERROR, REMOVE_ERROR, initialState, singletripReducer } from './SingleTripReducer';
import SearchIcon from '@material-ui/icons/Search';


function Singletrip(props){
    const [state, dispatch] = useReducer(singletripReducer, initialState);
    const { error } = state;    
    const history = useHistory();

    const validate = () => {
        let flag = true;
        if (!state.from) {
            dispatch({type: ERROR, name: 'from', value: 'Please enter a leaving place'});
            flag = false;
        }
        if (!state.to) {
            dispatch({
                type: ERROR,
                name: 'to',
                value: 'Please enter a destination',
            });
        }
        if (!state.departure) {
            dispatch({
                type: ERROR,
                name: 'departure',
                value: 'Please enter a departure date',
            });
        }
        return flag;
    }

    const onSubmit = () => {
        let query = {};
        query.leaving_from = state.from;
        query.going_to = state.to;
        query.departure = state.departure;
        query.adults = state.adults;
        query.childrens = state.childrens;
        query.infants = state.infants;
        if (validate()) {
            console.log(history);
            // history.push(`/search/flights?trip=single&query=${JSON.stringify(query)}`);
        }
    }

    useEffect(() => {
        if(state.from && error.from) {
            dispatch({type: REMOVE_ERROR, name: 'from'});
        }
    }, [state.from, error.from])

    useEffect(() => {
        if (state.to && error.to) {
            dispatch({type: REMOVE_ERROR, name: 'to'});
        }
    }, [state.to, error.to]);

    useEffect(() => {
        if (state.departure && error.departure) {
            dispatch({type: REMOVE_ERROR, name: 'departure'});
        }
    }, [state.departure, error.departure]);
    return(
        <div>
            <Grid container spacing={2}>
                <Grid item xs={12} md={3}>
                    <AutoComplete 
                        name='from'
                        value={state.from}
                        label='Leaving from'
                        onChange={(event, value) => dispatch({type: CHANGE, name: 'from', value: value})}
                        error={error.from}
                    />         
                </Grid>
                <Grid item xs={12} md={3}>
                    <AutoComplete 
                        name='to'
                        value={state.to}
                        label='Going to'
                        onChange={(event, value) => dispatch({type: CHANGE, name: 'to', value: value})}
                        error={error.to}
                    />    
                </Grid>
                <Grid item xs={12} md={3}>
                    <DateInput 
                        label='Departure'
                        name='departure'
                        value={state.departure}
                        onChange={(date) => dispatch({type: CHANGE, name: 'departure', value: date})}
                        error={error.departure}
                    />
                </Grid>
                <Grid item xs={12} md={3}>
                    <PassengerDetails 
                        state={state}
                    />
                </Grid>
            </Grid>
            <div className='m-t-12'>
                <Grid container justify='flex-end'>
                    <Button icon={<SearchIcon />} text='Search' onClick={onSubmit} />
                </Grid>
            </div>
        </div>
    );
}


export default Singletrip;