import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Button from '../../../../../../components/button';
import Text from '../../../../../../components/text';


const useStyles = makeStyles((theme) => ({
    container: {
        padding: theme.spacing(1.5),
        border: `1px solid #cecece`,
        borderRadius: 2,
    },
    photo: {
        height: 48,
        width: 48,
        borderRadius:2,
        overflow: 'hidden',
    },
    flightLine: {
        height: 1,
        width: '100%',
        backgroundColor: '#cecece',
    }
}));


function Flight(props){
    const classes = useStyles();
    const { flight } = props;
    return (
        <div className={classes.container}>
            <div className='r-f a-c'>
                <div className='m-r-12'>
                    <div className={classes.photo}>
                        <img src={flight.photo} alt={flight.title} />
                    </div> 
                    <Text text={flight.title} variant='meta' textAlign='center' />
                </div>
                <div className='f-g-1'>
                    <div className='r-f a-c'>
                        <div className='m-r-12'>
                            <Text text={flight.from.time} variant='title' textAlign='center' />
                            <Text text={flight.from.place} variant='meta' textAlign='center' />
                        </div>
                        <div className='f-g-1'>
                            <Text text='03h 55min' variant='meta' textAlign='center' />
                            <div className={classes.flightLine} />
                        </div>
                        <div className='m-l-12'>
                            <Text text={flight.to.time} variant='title' textAlign='center' />
                            <Text text={flight.to.place} variant='meta' textAlign='center' />
                        </div>
                    </div>
                </div>
                <div className='m-l-12'>
                    <Text text='Baggage' variant='title' />
                    <div className='r-f a-c'>
                        <Text text='Cabin:  7 Kgs      Check-in:  30 Kgs' variant='meta' />
                    </div>
                </div>
            </div>
            <div className='r-f j-e'>
                <div className='f-g-1' />
                <div className='m-l-12'>
                    <Button 
                        text='Change' 
                        variant='outlined' 
                        color='default'
                        size='small' 
                    />
                </div>
                <div className='m-l-12'>
                    <Button 
                        text='Remove' 
                        variant='outlined' 
                        color='default'
                        size='small' 
                    />
                </div>
            </div>
        </div>
    );
}

export default Flight;
