import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Text from '../../../../../../components/text';


const FACILITIES = ['Room size: 400 sqft', 'Bed type: Twin Bed', 'Sleeps: 2', 'View: Garden View', 'Work desk', 'Daily House Keeping', 'Closest'];


const useStyles = makeStyles((theme) => ({
    ul: {
        margin: 0,
        padding: 0,
        display: 'flex',
        flexDirection: 'row',
        flexWrap: 'wrap',
        width: '460px',
        [theme.breakpoints.down('sm')]: {
            width: '100%',
        }
    },
    li: {
        marginLeft: 18,
        marginRight: 12,
        marginTop: 6,
        marginBottom: 6,
        width: '192px',
        color: '#4a4a4a',
        // [theme.breakpoints.down('xs')]: {
        //     width: '100%',
        // }
    }
}));


function Facilities(props){
    const classes = useStyles();
    const { room } = props;
    return (
        <div>
            <div>
                <Text text={room.title} variant='sideheading' />
            </div>
            <div className='m-t-12'>
                <ul className={classes.ul}>
                    {
                        FACILITIES.map((facility, index) =>(
                            <li className={classes.li} key={index}>
                                <Text variant='meta' text={facility} />
                            </li>
                        ))
                    }
                </ul>
            </div>           
        </div>
    );
}



export default Facilities;
