import React, { useState, useEffect } from 'react';
import { useLocation, Redirect } from 'react-router-dom';
import AutoComplete from '../../../../../components/input/autocomplete';
import CheckboxGroup from '../../../../../components/input/checkboxGroup';
import DateInput from '../../../../../components/input/dateInput';
import SliderInput from '../../../../../components/input/sliderInput';
import Text from '../../../../../components/text';


const queryString = require('query-string');


function HolidaysForm(props){
    const location = useLocation();
    const queryStr = queryString.parse(location.search);
    const trip = queryStr.trip;
    const query = queryStr.query ? JSON.parse(queryStr.query) : {};

    const [prices, setPrices] = useState([1000, 4000]);
    const [duration, setDuration] = useState([3, 12]);

    const [url, setUrl] = useState('');
    const [isRedirect, setRedirect] = useState(false); 


    const onChange = (name, value) => {
        let newQuery = Object.assign({...query});
        newQuery[name] = value;
        setUrl(`/search/flights?trip=${trip}&query=${JSON.stringify(newQuery)}`);
        setRedirect(true);
    }

    useEffect(() => {
        if (isRedirect){
            setRedirect(false);
        }        
    }, [isRedirect]);

    if (isRedirect && url) {
        console.log('Redirecting...\n');
        return(
            <Redirect to={url} />
        );
    }
    return (
        <div>
            <div className='p-tb-12'>
                <AutoComplete 
                    label='Leaving From'
                    name='leaving_from'
                    value={query.leaving_from}
                    onChange={(event, newValue) => onChange('leaving_from', newValue)}
                />
            </div>
            <div className='p-tb-12'>
                <AutoComplete 
                    label='Going to'
                    name='going_to'
                    value={query.going_to}
                    onChange={(event, newValue) => onChange('destination', newValue)}
                />
            </div>
            <div className='p-tb-12'>
                <DateInput 
                    label='Departure date'
                    onChange={(date)  => onChange('departure', date)}
                    value={query.departure}
                />
            </div>
            {
                query.return ?
                    <div className='p-tb-12'>
                        <DateInput 
                            label='Return date'
                            onChange={(date) => onChange('return', date)}
                            value={query.return}
                        />
                    </div>
                :
                    null
            }
            
            <div className='p-tb-12'>
                <Text text='Preferences' variant='sideheading' />
            </div>
            <div className='p-tb-12'>
                <CheckboxGroup
                    label='AIRCRAFT TYPE'
                    state={[
                        {
                            name: 'turbo_prop',
                            checked: false,
                            label: 'Turbo prop'
                        },
                        {
                            name: 'light_get',
                            checked: false,
                            label: 'Light Jet'
                        },
                        {
                            name: 'medium_jet',
                            checked: false,
                            label: 'Medium Jet'
                        },
                        {
                            name: 'heavy_jet',
                            checked: false,
                            label: 'Heavy Jet'
                        }
                    ]}
                />
            </div>
            <div className='p-tb-12'>
                <CheckboxGroup
                    label='Aircraft Facilities'
                    state={[
                        {
                            name: 'lavatory',
                            checked: false,
                            label: 'Lavatory'
                        },
                        {
                            name: 'flight_attendant',
                            checked: false,
                            label: 'Flight Attendant'
                        },
                        {
                            name: 'gallery',
                            checked: false,
                            label: 'Gallery'
                        },       
                    ]}
                />
            </div>
            <div className='p-tb-12'>
                <SliderInput
                    label='Prices'
                    name='price'
                    value={prices}
                    onChange={(event, value) =>{console.log(value); setPrices(value)}}
                    min={1000}
                    max={4000}
                />
            </div>
            <div className='p-tb-12'>
                <SliderInput
                    label='Cabin HEIGHT'
                    name='height'
                    value={duration}
                    onChange={(event, value) => setDuration(value)}
                    min={3}
                    max={12}
                />
            </div>
        </div>
    );
}


export default HolidaysForm;
