import React from 'react';
import MaterialChip from '@material-ui/core/Chip';
import { makeStyles } from '@material-ui/core/styles';


const useStyles = makeStyles((theme) => ({
    success: {
        borderRadius: 2,
        // backgroundColor: '#04c404',
        backgroundColor: '#33d18f',
        color: '#fff',
    },
    danger: {
        borderRadius: 2,
        backgroundColor: '#eb2026',
        color: '#fff',
    },
    chip: {
        borderRadius: 2,
        // backgroundColor: 'rgba(2, 182, 240, 0.16)',
    },
    defaultChip: {
        borderRadius: 2,
        backgroundColor: 'rgba(2, 182, 240, 0.16)',
    }
}));


function Chip(props){
    const classes = useStyles();
    const { color } = props;
    return (
        <MaterialChip 
            className={color === 'success' ? classes.success : color === 'danger' ? classes.danger : color === 'primary' ? classes.chip : classes.defaultChip} 
            color={color === 'primary' ? 'primary' : color === 'secondary' ? 'secondary' : 'default'} 
            label={props.label}
            size={props.size ? props.size : undefined}
        />
    );
}


export default Chip;
