import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles'
// import ReactImageZoom from 'react-image-zoom';
import ImageZoom from './imageZoom'

const useStyles = makeStyles((theme) => ({
    container: {
        width: 424,
    },
    root: {
        padding: theme.spacing(1.5),
        border: `1px solid #cecece`,
        borderRadius: 2,
    },
    thumbnail: {
        width: 72,
        marginBottom: theme.spacing(0.5),
        height: 'auto',
        cursor: 'pointer'
    },
    active: {
        border: `2px solid ${theme.palette.primary.main}`,
        borderRadius: 2,
    },
    photo: {
        marginLeft: theme.spacing(0.5),
        width: `calc(100% - ${72 + theme.spacing(0.5)}px)`,
        position: 'relative',
    }
}));


function Photo(props){
    const classes = useStyles();
    const [index, setIndex] = useState(0);

    const onChange = (index) => {
        setIndex(index);
    };

    const {isLoading, product} = props;
    console.log(product);
    return (
        <div className={classes.container}>
            <div className={classes.root}>
                <div className='r-f'>
                    <div className={''}>
                        {
                            isLoading ?
                                null
                            :
                                <div>
                                    {
                                        product.photo_set.map((photo, i) => (
                                            <div 
                                                className={`${classes.thumbnail} ${i === index ? classes.active : ''}`}
                                                onMouseEnter={() => onChange(i)}
                                            >
                                                <img src={photo.image} alt='' />
                                            </div>
                                        ))
                                    }
                                </div>
                        }
                    </div>
                    <div className={classes.photo} style={{position: 'relative'}}>
                        {
                            isLoading ?
                                null
                            :
                                // <ReactImageZoom
                                //     img={product.photo_set[index].image}
                                //     width={'100%'}
                                //     height={'auto'}
                                //     scale={0.25}
                                //     zoomPosition='default'
                                //     // zoomStyle={{postion: 'absolute', left: '402px'}}
                                // >
                                <ImageZoom 
                                    src={product.photo_set[index].image}
                                />

                        // </ReactImageZoom>
                        }
                        
                    </div>
                </div>
               
               
            </div>
        </div>
       
        
    );
}


export default Photo;