import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Text from '../../../../components/text';


const useStyles = makeStyles((theme) => ({
    box: {
        marginTop: theme.spacing(1.5),
        paddingLeft: theme.spacing(1.5),
        paddingRight: theme.spacing(1.5),
        border: '1px solid #cecece',
        borderRadius: 2,
    }
}));


const SPECIFICATIONS = [
    {
        label:'Baggage Volume',
        value: '400 Kgs',

    },
    {
        label: 'Pilot',
        value: '2'
    },
    {
        label: 'Seats',
        value: 12,
    },
    {
        label: 'Wifi',
        value: 'Not Available',
    },
    {
        label: 'Cabin Height',
        value: '6ft',
    },
    {
        label: 'Sat Phone',
        value: 'Not Available',
    },
    {
        label: 'Flight Attendant',
        value: 'Available',
    },
    {
        label: 'Lavatory',
        value: 'Not Available',
    },
    {
        label: 'Speed',
        value: '360miles/hr',
    }
]


function Specification(props){
    const classes = useStyles();
    return (
        <div className='m-t-18'>
            <div>
                <Text text='Specifications' variant='sideheading' />
            </div>
            <div className={classes.box}>
                {
                    SPECIFICATIONS.map((spec, index) => (
                        <div className='r-f a-c p-tb-12' key={index}>
                            <div>
                                <Text text={spec.label} variant='text' />
                            </div>
                            <div className='f-g-1' />
                            <div>
                                <Text text={spec.value} variant='text' />
                            </div>
                        </div>
                    ))
                }
            </div>
        </div>
    );
}


export default Specification;
