import React, { useEffect, useRef, useState } from 'react';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import { makeStyles } from '@material-ui/core/styles';
import BackButton from '../../../components/backButton';
import Text from '../../../components/text';
import FavouriteButton from '../../../components/favouriteButton';
import Skeleton from '@material-ui/lab/Skeleton';


const useStyles = makeStyles((theme) => ({
    appBar: {
        height: 112,
        minHeight: 112,
        transition: 'all 350ms linear'
    },
    appBarTranslate: {
        transform: 'translateY(-64px)'
    },
    toolbar: {
        minHeight: 64,
    }
}));


function Header(props){
    const classes = useStyles();

    const [isTranslate, setTranslate] = useState(false);
    const previousScroll = useRef();

    useEffect(() => {
        previousScroll.current = 0; 
    }, []);

    useEffect(() => {
        const onScroll = () => {
            let scrollTop = document.documentElement.scrollTop;
            if (scrollTop > 64 && scrollTop > previousScroll.current) {
                setTranslate(true);
            } else if (scrollTop < previousScroll.current) {
                setTranslate(false);
            } else if (scrollTop < 64) {
                setTranslate(false);
            }
            if (scrollTop > previousScroll.current + 64 || scrollTop < previousScroll.current - 64) {
                previousScroll.current = scrollTop;
            }
        }
        window.addEventListener('scroll', onScroll);
        return () => {
            window.removeEventListener('scroll', onScroll);
        }
    }, [previousScroll]);

    const { isLoading } = props;
    return (
        <AppBar 
            className={`${classes.appBar} ${isTranslate ? classes.appBarTranslate : ''}`}
            color='inherit' 
            position='fixed'
        >
            <Toolbar className={classes.toolbar}>
                <div className='m-r-12'>
                    {
                        isLoading ?
                            <Skeleton variant='square' style={{height: 24, width: 24, borderRadius: 12}} />
                        :
                            <BackButton />
                    }
                   
                </div>
                <div className='f-g-1'>
                    {
                        isLoading ?
                            null
                        :
                            <React.Fragment>
                                <Text text={props.title} variant='title' />
                                <Text text={props.info} variant='meta' />
                            </React.Fragment>
                    }
                    
                </div>
                <div className='m-l-12'>
                    <FavouriteButton {...props} />
                </div>
            </Toolbar>
            {
                props.tabbar ?
                    props.tabbar
                :
                    null
            }
        </AppBar>
    );
}


export default Header;
