import React, { useState, useRef } from 'react';
import { makeStyles } from '@material-ui/core/styles';

const useStyle = makeStyles((theme) => ({
    root: {
        width: '100%',
        position: 'relative',
    },
    photo: {
        width: '100%',
        position: 'relative',
        cursor: 'pointer',
    },
    img: {
        width: '100%',
        height: 'auto',
    },
    panel: {
        width: 80,
        height: 100,
        backgroundColor: 'rgba(255, 255, 255, 0.72)',
        position: 'absolute',
        zIndex: 10,
        cursor: 'move',
        // transition: 'all 350ms linear',
    },
    zoomContainer: {
        position: 'absolute',
        left: '105%',
        top: 0,
        zIndex: 20,
        borderRadius: 2,
        boxShadow: theme.shadows[2],
        height: 300,
        width: 240,
        overflow: 'hidden',
    },
    zoomedImage: {
        height: '100%',
        width: '100%',
        transform: 'scale(3.0)',
        left: 240,
        top: 300,
        position: 'absolute',
    }
}));


function ImageZoom(props){
    const classes = useStyle();
    const photoRef = useRef();
    const [isHover, setHover] = useState(false);
    const [position, setPosition] = useState({x: 40, y: 50});
    const onMove = (event) => {
        const { offsetX, offsetY } = event.nativeEvent;
        let x, y;
        if (offsetX < 40) {
            x = 40
        } else if (offsetX > photoRef.current.offsetWidth - 40) {
            x = photoRef.current.offsetWidth - 40;
        } else {
            x = offsetX;
        }
        if (offsetY < 50) {
            y = 50;
        } else if (offsetY > photoRef.current.offsetHeight - 50) {
            y = photoRef.current.offsetHeight - 50;
        } else {
            y = offsetY;
        }
        setPosition({x: x, y: y});
    }
    return(
        <div className={classes.root}
            onMouseEnter={() => setHover(true)} 
            onMouseLeave={() => setHover(false)}
        >
            <div 
                className={classes.photo}                 
                onMouseMove={isHover ? onMove : null}
                ref={photoRef}
            >
                <img src={props.src} alt='' className={classes.img} />
                
            </div>    
            {
                isHover &&
                    <div className={classes.panel} style={{left: position.x - 40, top: position.y - 50}}>

                    </div>
            } 
            {
                isHover &&
                    <div className={classes.zoomContainer}>
                        <img 
                            src={props.src} 
                            alt='' 
                            className={classes.zoomedImage} 
                            style={{transform: `scale(3.0) translate(-${(position.x - 40)}px, 0px)`}}    
                        />
                    </div>
            }      
        </div>
    );
}

export default ImageZoom;