import React, { useEffect, useState } from 'react';
import { Redirect, useLocation } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import AutoComplete from '../../../../../components/input/autocomplete';
import DateInput from '../../../../../components/input/dateInput';

const QueryString = require('query-string');


const useStyles = makeStyles((theme) => ({
    container: {
        paddingTop: theme.spacing(1.5),
        paddingRight: theme.spacing(1.5),
        paddingBottom: theme.spacing(0.5),
        paddingLeft: theme.spacing(1.5),
        borderRadius: 2,
        backgroundColor: 'rgba(255, 127, 80, 0.16)',
    }
}));

function HolidayForm(props){
    const [redUrl, setRedUrl] = useState(null)
    const location = useLocation();
    const query = QueryString.parse(location.search).query ? JSON.parse(QueryString.parse(location.search).query) : {};
    const onChange = (name, value) => {
        if (value) {
            let newQuery = {...query, [name]: value};
            setRedUrl(`${location.pathname}?query=${JSON.stringify(newQuery)}`);
        }
        
    }

    useEffect(() => {
        if (redUrl) {
            setRedUrl(null);
        }
    }, [redUrl]);

    const classes = useStyles();

    if (redUrl) {
        return (
            <Redirect to={redUrl} />
        );
    }
    return (
        <div className={classes.container}>
            <Grid container spacing={1}>
                <Grid item xs={12} sm={6}>
                    <AutoComplete 
                        label='Leaving From'
                        name='leaving_from'
                        variant='standard'
                        value={query.leaving_from}
                        onChange={(event, newValue) => onChange('leaving_from', newValue)}
                    />
                </Grid>
                <Grid item xs={12} sm={6}>
                    <DateInput 
                        label='Departure Date'
                        name='departure'
                        value={query.departure || null}
                        inputVariant='standard'
                        onChange={(date) => onChange('departure', date)}
                    />
                </Grid>
                <Grid item xs={12} sm={6}>
                    
                </Grid>
            </Grid>
        </div>
    );
}


export default HolidayForm;