export const initialState = {
    from: '',
    to: '',
    departure: null,
    adults: 1,
    childrens: 0,
    infants: 0,
    class: 'economy'
}


export const CHANGE = 'CHANGE';


export function flightReducer(state, action){
    switch (action.type) {
        case CHANGE: 
            return {...state, [action.name]: action.value}
        default:
            return state;
    }
}