import React, { useState } from 'react';
import Slider from '@material-ui/core/Slider';
import { makeStyles } from '@material-ui/core/styles';
import { Text } from '../../../components'
import HotelClass from './hotelClass';


const useStyles = makeStyles((theme) => ({
    filterContainer: {
        padding: theme.spacing(1.5),
        border: `1px solid #cecece`,
    }
}))


function valuetext(value) {
    return `$${value}`;
}
  

function Filter(props){
    const [priceRange, setPriceRange] = useState([1000, 4000]);
    const classes = useStyles();
    return (
        <div className=''>
            <div className={classes.filterContainer}>
                <Text variant='sideheading' text='Price' />
                <div className='p-lr-12'>
                    <Slider 
                        value={priceRange}
                        onChange={(event, newValue) => setPriceRange(newValue)}
                        valueLabelDisplay="auto"
                        aria-labelledby="range-slider"
                        getAriaValueText={valuetext}
                        min={1000}
                        max={4000}
                    />
                </div>
                <div className=''>
                    <div className='r-f a-c'>
                        <Text text='1000$' variant='text' />
                        <div className='f-g-1' />
                        <Text text='4000$+' variant='text' />
                    </div>
                    
                </div>
                <HotelClass />
            </div>
            
        </div>
    );
}


export default Filter;
