import React from 'react';
import { DATA } from './data';
import Cardlist from '../../../components/cardlist';


function Recommendation(props){
    return(
        <div>
            <Cardlist 
                result={DATA[0]}
            />
            <Cardlist 
                result={DATA[1]}
            />
            <Cardlist 
                result={DATA[2]}
            />
        </div>
    );
}


export default Recommendation;