import React, { useState, useEffect, useRef } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Button from '../../../../../components/button';
import Text from '../../../../../components/text';


const useStyles = makeStyles((theme) => ({
    container: {
        // marginTop: theme.spacing(1.5),
        // padding: theme.spacing(1.5),
        // border: `1px solid rgba(0, 0, 0, 0.08)`,
        // backgroundColor: 'rgba(2, 182, 240, 0.16)',
        // borderRadius: 2,
        // position: 'fixed',
        // top: 
    },
    // footer: {
    //     borderTop: `1px solid rgba(0, 0, 0, 0.08)`,
    // }
}));


function Prices(props){
    const [isFixed, setFixed] = useState(false);
    const [style, setStyle] = useState(null);
    const containerRef = useRef();
    useEffect(() => {
        const onScroll = () => {
            const scrollTop = document.documentElement.scrollTop;
            if (scrollTop > 125) {
                setFixed(true);
            } else {
                setFixed(false);
            }
        }
        window.addEventListener('scroll', onScroll);
        return () => {
            window.removeEventListener('scroll', onScroll);
        }
    }, []);

    useEffect(() => {
        if (isFixed) {
            setStyle({
                top: containerRef.current.offsetTop,
                left: containerRef.current.offsetLeft,
                width: containerRef.current.offsetWidth,
                position: 'fixed',
            });
        } else {
            setStyle(null);
        }
    }, [isFixed])
    const classes = useStyles();
    console.log(style);
    return(
        <div className={classes.container} ref={containerRef} style={isFixed && style ? style : {}}>
            <Card>
                <CardContent>
                    <div className='p-12'>
                        <Text text='Estimated Price' variant='meta' textAlign='center' />
                        <Text text='$1000' variant='heading' textAlign='center' />
                    </div>
                    <div className='r-f j-c'>
                        <Button text='Book Now' />
                    </div>
                </CardContent>     
            </Card>                 
        </div>
    );
}


export default Prices;