import { useState } from 'react';



function useMenu(){
    const [anchorEl, setAnchorEl] = useState(null);
    const onOpen = (event) => {
        setAnchorEl(event.target);
    };
    const onClose = (event) => {
        setAnchorEl(null);
    }
    return {
        anchorEl,
        onOpen,
        onClose
    };
}


export default useMenu;