import React from 'react';
import { Link } from 'react-router-dom';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import { makeStyles } from '@material-ui/core/styles';
import Text from '../../../../components/text';


const useStyles = makeStyles((theme) => ({
    media: {
        paddingTop: '56.66%',
    },
    item: {
        width: '33.333333%',
        minWidth: '33.333333%',
    }
}));

function InvestCard(props){
    const { item } = props;
    const classes = useStyles();
    return (
        <Link to={`/invest/${1}`}>
            <div>
                <Card>
                    <CardMedia 
                        image={item.image}
                        className={classes.media}
                    />
                    <CardContent>
                        <Text text={item.title} variant='title' />
                        <Text text={`${item.portfolio} - ${item.category}`} variant='meta' />
                        <div className='r-f a-c m-t-12'>
                            <div className={classes.item}>
                                <div className='c-f j-c h-100 w-100'>
                                    <Text text={`$${item.total}`} variant='title' />
                                    <Text text='Raised' variant='meta' />
                                </div>
                            </div>
                            <div className={classes.item}>
                                <div className='c-f j-c h-100 w-100'>
                                    <Text text={`${item.investors}`} variant='title' />
                                    <Text text='Investors' variant='meta' />
                                </div>
                            </div>
                            <div className={classes.item}>
                                <div className='c-f j-c h-100 w-100'>
                                    <Text text={`${item.days}`} variant='title' />
                                    <Text text='Days left' variant='meta' />
                                </div>
                            </div>
                        </div>
                    </CardContent>
                </Card>
            </div>
        </Link>
    );
}

export default InvestCard;
