import React from 'react';
import Buy from '@material-ui/icons/FlashOnRounded';
import Button from '../../../../../../../components/button';


function BuyButton(props){
    return (
        <div>
           <Button 
                color='primary'
                text='Buy Now'
                fullWidth={true}
                icon={
                    <Buy fontSize='small' /> 
                }
           /> 
        </div>
    );
}


export default BuyButton;
