export const initialState = {
    destination: '',
    check_in: null,
    check_out: null,
    adults: 1,
    childrens: 0,
    infants: 0,
    class: 'economy',
    error: {
        destination: null,
        check_in: null,
        check_out: null,
        adults: null,
        childrens: null,
        infants: null,
    }
}


export const CHANGE = 'CHANGE';
export const ERROR = 'ERROR';
export const REMOVE_ERROR = 'REMOVE_ERROR';


export function hotelReducer(state, action){
    switch (action.type) {
        case CHANGE: 
            return {...state, [action.name]: action.value};
        case ERROR:
            return {...state, error: {...state.error, [action.name]: action.value}};
        case REMOVE_ERROR:
            return {...state, error: {...state.error, [action.name]: null}};
        default:
            return state;
    }
}