import React from 'react';
import { Link } from 'react-router-dom';
import { Text } from '../../../../components';
import { makeStyles } from '@material-ui/core/styles';


const useStyles = makeStyles((theme) => ({
    container: {
        border: '1px solid #cecece',
        borderRadius: 2,
        '&:hover': {
            borderColor: theme.palette.primary.main,
        },
        '&:not(:last-child)': {
            marginBottom: 12,
        }
    },
    content: {
        padding: 12,
        flexGrow: 1,
    },
    photo: {
        marginRight: 12,
        height: 200,
        width: 320,
    },
    icon: {
        marginRight: 6,
        height: 20,
        width: 20,
        color: '#4a4a4a'
    },
    priceContainer: {
        padding: '12px 24px',
        borderRadius: 2,
        backgroundColor: 'rgba(2, 182, 240, 0.16)',
    },
    infoItem: {
        width: '50%',
        marginBottom: 6,
    }
}))

function FlightItem(props){
    const classes = useStyles();
    const { flight } = props;
    return(
        
        <div className={classes.container}>
            <Link to={`/flight/1`}>
                <div className='r-f'>
                    <div className={classes.content}>
                        <div className='r-f'>
                            <div className={classes.photo}>
                                <img src={flight.photo} alt={flight.title} />
                            </div>
                            <div className='f-g-1'>
                                <div className='r-f a-c'>
                                    <div>
                                        <Text variant='sideheading' text={flight.title} />
                                    </div>
                                </div>                        
                                <div className=''>
                                    <Text variant='meta' text={flight.engine} />
                                </div>
                                <div className='m-t-12'>
                                    <div className='r-f a-c'>
                                        <div>
                                            <Text text='Base:' variant='meta' />
                                        </div>
                                        <div className='m-l-6'>
                                            <Text text={flight.base} variant='title' />
                                        </div>
                                    </div>
                                </div>
                                <div className='m-t-12'>
                                    <div className='r-f-w'>
                                        <div className={classes.infoItem}>
                                            <div className='r-f a-c'>
                                                <div className='m-r-6'>
                                                    <Text text='Baggage:' variant='meta' />
                                                </div>
                                                <div>
                                                    <Text text={`${flight.baggage}Kg`} variant='title' />
                                                </div>
                                            </div>
                                        </div>
                                        <div className={classes.infoItem}>
                                            <div className='r-f a-c'>
                                                <div className='m-r-6'>
                                                    <Text text='Pilots:' variant='meta' />
                                                </div>
                                                <div>
                                                    <Text text={flight.pilot} variant='title' />
                                                </div>
                                            </div>
                                        </div>
                                        <div className={classes.infoItem}>
                                            <div className='r-f a-c'>
                                                <div className='m-r-6'>
                                                    <Text text='Seats:' variant='meta' />
                                                </div>
                                                <div>
                                                    <Text text={flight.seat} variant='title' />
                                                </div>
                                            </div>
                                        </div>
                                        <div className={classes.infoItem}>
                                            <div className='r-f a-c'>
                                                <div className='m-r-6'>
                                                    <Text text='Cabin Height:' variant='meta' />
                                                </div>
                                                <div>
                                                    <Text text={`${flight.cabin_height}ft`} variant='title' />
                                                </div>
                                            </div>
                                        </div>
                                        <div className={classes.infoItem}>
                                            <div className='r-f a-c'>
                                                <div className='m-r-6'>
                                                    <Text text='Speed:' variant='meta' />
                                                </div>
                                                <div>
                                                    <Text text={`${flight.speed}miles/hr`} variant='title' />
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        </div>
                        <div className={`${classes.priceContainer} r-f a-c`}>
                            <div className='c-f h-100 w-100 j-e'>
                                <div className='f-g-1' />
                                <div className='f-g-1' />
                                <div className='m-t-8'>
                                    <div>
                                        <Text text={`$${flight.price}`} textAlign='center' variant='heading' />
                                    </div>
                                    <div>
                                        <Text text='Estimated Price' textAlign='center' variant='meta' />
                                    </div>
                                </div>
                                <div className='f-g-1' />
                            </div>                            
                        </div>
                    </div>
                </Link>
            </div>
    );
}


export default FlightItem;