import React from 'react';
import { Link } from 'react-router-dom';
import MaterialCard from '@material-ui/core/Card';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import { makeStyles } from '@material-ui/core/styles';
import Chip from '../chip';
import FavouriteButton from '../favouriteButton';
import Text from '../text';


const useStyles = makeStyles((theme) => ({
    card: {
        // boxShadow: '0 1px 6px 0 rgba(0, 0, 0, 0.2)',
    },
    photo: {
        width: '100%',
        paddingTop: '56%',
        position: 'relative',
    },
    favouriteButton: {
        right: 6,
        bottom: 6,
        backgroundColor: 'rgba(0, 0, 0, 0.5)',
        position: 'absolute',
        zIndex: 10,
        height: 44,
        width: 44,
        borderRadius: 22,
    },
    offer: {
        position: 'absolute',
        bottom: -8,
        left: 12,
    }
}));


function Card(props){
    const classes = useStyles();
    const { item } = props;
    let link = '/', meta;
    if (item.type === 'holiday') {
        link = '/holiday/1';
        meta = `${item.days}N.${item.days + 1}D - ${item.location}`;
    } else if (item.type === 'hotel') {
        link = '/hotel/1';
    } else if (item.type === 'flight') {
        link = '/flight/1';
    } else if (item.type === 'cab') {
        link = '/cab/1';
    } else if (item.type === 'destination') {
        link = `/search/holidays?${JSON.stringify({destination: item.title})}`;
    }
    console.log(link);
    return (
        <MaterialCard className={classes.card}>
            <CardMedia image={item.photo} className={classes.photo} >
                {
                    item.type !== 'destination' ?
                        <React.Fragment>
                            <div className={classes.favouriteButton}>
                                <FavouriteButton color='white' item={item} />
                            </div>
                            {
                                item.offer || true ?
                                    <div className={classes.offer}>
                                        <Chip label={`${item.offer}% offer`} color='success' size='small' />
                                    </div>
                                :
                                    null
                            }
                        </React.Fragment>
                    :
                        null
                }                
            </CardMedia>
            <CardContent>
                <Link to={'#'}>
                    <Text variant={item.type === 'destination' ? 'h5' : 'title' } text={item.title} />
                </Link>
                {
                    item.type === 'destination' ?
                        <React.Fragment>
                            <Text variant='meta' text='Price starts from' />
                            <Text text={` ${item.price}`} variant='heading' color='primary' />
                            <Text text='per person' variant='meta' /> 
                        </React.Fragment>
                    :   
                        <React.Fragment>
                            <Text variant='meta' text={meta} />
                            <div className='r-f a-c'>
                                <div>
                                    <Text variant='meta' text={` ${item.price}`} strike={true} />
                                </div>
                                <div className='m-l-12'>
                                    <Text variant='sideheading' text={` ${item.new_price}`} color='primary' />
                                </div>
                            </div>
                        </React.Fragment>
                }                        
            </CardContent>
        </MaterialCard>
    );
}

export default Card;
