import React, { useEffect, useRef, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import ChevronLeft from '@material-ui/icons/ChevronLeftRounded';
import ChevronRight from '@material-ui/icons/ChevronRightRounded';
import useScreen from '../../hooks/useScreen';
import Card from '../card';
import Text from '../text';
import Icon from '../icon';


const useStyles = makeStyles((theme) => ({
    heading: {
        marginBottom: theme.spacing(1),
    },
    sliderContainer: {
        marginBottom: theme.spacing(1.5),
        overflow: 'hidden',
    },
    slider: {
        transition: `all 350ms linear`,
    },
    item: {
        paddingRight: theme.spacing(1.25),
        paddingLeft: theme.spacing(0.25),
        marginBottom: theme.spacing(0.25),
        [theme.breakpoints.down('md')]: {
            padding: theme.spacing(0.25),
            marginBottom: theme.spacing(1.5),
        }
    },
    dItem: {
        width: '20%',
        [theme.breakpoints.down('md')]: {
            width: '25%',
            // marginBottom: theme.spacing(1.5),
        },
        [theme.breakpoints.down('sm')]: {
            width: '50%',
        },
        [theme.breakpoints.down('xs')]: {
            width: '100%',
            // marginBottom: theme.spacing(1.5),
        }
    },
    item1: {
        width: '100%',
        minWidth: '100%',
        // marginBottom: theme.spacing(2),
    },
    item4: {
        width: '25%',
        minWidth: '25%',
    },
    item3: {
        width: '33.333333%',
        minWidth: '33.333333%',
        // marginBottom: theme.spacing(2),
    },
    item2: {
        width: '50%',
        minWidth: '50%',
        // marginBottom: theme.spacing(2),
    },
    icon: {
        height: 44,
        width: 44,
    }
}));

function Cardlist(props){
    const { screenWidth } = useScreen();
    const { result } = props;
    const { itemList } = result;
    const classes = useStyles();

    const containerRef = useRef();
    const itemRef = useRef();
    const [margin, setMargin] = useState(0);
    const [itemLeft, setItemLeft] = useState(0);
    const [itemInPage, setItemInPage] = useState(0);

    const onRightClick = (event) => {
        const sliderWidth = containerRef.current.offsetWidth;
        const itemWidth = itemRef.current.offsetWidth;
        const itemInPage = parseInt(sliderWidth / itemWidth, 10);
        console.log(itemInPage);
        let newMargin, newItemLeft;
        if (itemList.length - itemInPage + itemLeft < itemInPage) {
            newMargin = margin - ((itemList.length - itemInPage + itemLeft) * itemWidth);
            newItemLeft = itemList.length - itemInPage;
        } else {
            newMargin = margin - sliderWidth;
            newItemLeft = itemLeft + itemInPage;
        }
        setMargin(newMargin);
        setItemLeft(newItemLeft);
        console.log(newMargin);
        console.log(newItemLeft);
    }

    const onLeftClick = (event) => {
        const sliderWidth = containerRef.current.offsetWidth;
        const itemWidth = itemRef.current.offsetWidth;
        const itemInPage = parseInt(sliderWidth / itemWidth, 10);
        let newMargin, newItemLeft;
        if (itemLeft >= itemInPage) {
            newMargin = margin + sliderWidth;
            newItemLeft = itemLeft - itemInPage;
        } else {
            newMargin = 0;
            newItemLeft = 0;
        }
        setMargin(newMargin);
        setItemLeft(newItemLeft);
    }

    useEffect(() => {
        console.log('Use Hook');
        setItemInPage(parseInt(containerRef.current.offsetWidth / itemRef.current.offsetWidth, 10));
        // console.log(itemInPage);
    }, [containerRef, itemRef]);
    console.log(itemInPage);
    return (
        <div className=''>
            <div className={`${classes.heading} r-f a-c`}>
                <div className='f-g-1'>
                    <Text text={result.title} variant='sideheading' />
                </div>
                {
                    screenWidth > 900 ?
                        <div className='r-f a-c'> 
                            <div className={classes.icon}>
                                {
                                    itemLeft > 0 ?
                                        <Icon 
                                            icon={<ChevronLeft fontSize='small' />}
                                            onClick={onLeftClick}
                                        />
                                    :
                                        null
                                }
                               
                            </div>
                            <div className={classes.icon}>
                                {
                                    itemList.length > (itemInPage + itemLeft) ?
                                        <Icon 
                                            icon={<ChevronRight fontSize='small' />}
                                            onClick={onRightClick}
                                        />
                                    :
                                        null
                                }
                               
                            </div>
                        </div>
                    :
                        null
                }
            </div>
            <div className={classes.sliderContainer} ref={containerRef}>
                <div className={`${screenWidth > 900 ? 'r-f' : 'r-f-w'} ${classes.slider}`} style={{transform: `translateX(${margin}px)`}}>
                    {
                        itemList.map((item, index) => (
                            <div className={`${classes.item} ${item.type === 'destination' ? classes.dItem : screenWidth > 800 ? classes.item4 : screenWidth < 500 ? classes.item1 : classes.item2}`} key={index} ref={index === 0 ? itemRef : null}>
                                <Card item={item} />
                            </div>
                        ))
                    }
                </div>
            </div>
        </div>
        
        // <Grid container spacing={2}>
        //     {
        //         itemList.map((item, index) => (
        //             <Grid 
        //                 item 
        //                 xs={screenWidth < 500 ? 12 : 6 } 
        //                 sm={screenWidth < 800 ? 6 : 4}
        //                 md={3}
        //                 key={index}
        //             >
        //                 <Card item={item} />
        //             </Grid>
        //         ))
        //     }
        // </Grid>
    );
}


export default Cardlist;
