import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Skeleton from '@material-ui/lab/Skeleton';


const useStyles = makeStyles((theme) => ({
    container: {
        border: '1px solid #cecece',
        borderRadius: 2,
        '&:not(:last-child)': {
            marginBottom: 12,
        }
    },
    content: {
        padding: 12,
        flexGrow: 1,
    },
    photo: {
        marginRight: 12,
        height: 200,
        width: 260,
        minWidth: 260,
    },
    icon: {
        marginRight: 6,
        height: 20,
        width: 20,
        color: '#4a4a4a'
    },
    priceContainer: {
        padding: '12px 24px',
        borderRadius: 2,
        backgroundColor: 'rgba(2, 182, 240, 0.16)',
        minWidth: 168,
    },
    title: {
        width: '75%',
    },
    meta: {
        width: '50%',
    }
}))

function HolidayItem(props){
    const classes = useStyles();
    return(        
        <div className={classes.container}>
            <div className='r-f'>
                <div className={classes.content}>
                    <div className='r-f'>
                        <Skeleton variant='rect' className={classes.photo} />
                        <div className='f-g-1'>
                            <div className=''>
                                <div>
                                    <Skeleton variant='text' className={classes.title} />
                                </div>
                            </div>                        
                            <div className=''>
                                <Skeleton variant='text' className={classes.meta} />
                            </div>
                        </div>
                    </div>
                </div>
                <div className={`${classes.priceContainer} r-f a-c`}>
                    <div className='c-f h-100 w-100 j-e'>
                        <div className='r-f j-c'>
                            <Skeleton variant='text' className={classes.meta} />
                        </div>
                        <div className='m-t-12 r-f j-c'>
                            <Skeleton variant='text' className={classes.title} />
                        </div>
                        <div className='m-t-8'>
                            <div className='r-f j-c'>
                                <Skeleton variant='text' className={classes.meta} />
                            </div>
                        </div>
                    </div>                            
                </div>            
            </div>
        </div>
    );
}


export default HolidayItem;