import React from 'react';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import useScreen from '../../../../hooks/useScreen';
import Text from '../../../../components/text';
import AccessTimeRoundedIcon from '@material-ui/icons/AccessTimeRounded';
import FlightLandRoundedIcon from '@material-ui/icons/FlightLandRounded';
import FlightTakeoffRoundedIcon from '@material-ui/icons/FlightTakeoffRounded';


const useStyles = makeStyles((theme) => ({
    item: {
        paddingTop: theme.spacing(2),
        paddingBottom: theme.spacing(2),
        paddingLeft: theme.spacing(1.5),
        paddingRight: theme.spacing(1.5),
        borderBottom: `1px solid #cecece`,
        borderLeft: `1px solid #cecece`,
        borderRight: `1px solid #cecece`,
        '&:first-child': {
            borderTopLeftRadius: 2,
            borderTopRightRadius: 2,
            borderTop: `1px solid #cecece`,
        },
        '&:last-child': {
            borderBottomLeftRadius: 2,
            borderBottomRightRadius: 2,
        }
    }
}));


function InfoItem(props){
    const { item } = props;
    const classes = useStyles();
    const { screenWidth } = useScreen();
    return (
        <div className={classes.item}>
            <Grid container spacing={2}>
                {
                    screenWidth >= 960 ?
                        <Grid item md={2}>
                            <div className='c-f a-c j-c h-100'>
                                <Text variant='text' text={item.leave_date} textAlign='center' />
                                <Text variant='text' text={item.leave_time} textAlign='center'  />
                            </div>                    
                        </Grid>
                    :
                        null
                }               
                <Grid item xs={4} md={2}>
                    <div className='c-f a-c j-c h-100'>
                        <div className='r-f j-c'>
                            <FlightTakeoffRoundedIcon />
                        </div>
                        <div className='m-t-12'>
                            <Text variant='text' text={item.leave_place} textAlign='center' />
                        </div>
                        {
                            screenWidth < 960 ?
                                <div className='m-t-12'>
                                    <Text variant='text' text={item.leave_date} textAlign='center' />
                                    <Text variant='text' text={item.leave_time} textAlign='center'  />
                                </div>
                            :
                                null
                        }
                    </div>
                </Grid>
                <Grid item xs={4}>
                    <div className='c-f a-c j-c h-100 w-100'>
                        <div className='r-f j-c'>
                            <AccessTimeRoundedIcon />
                        </div>
                        <div className='m-t-12'>
                            <Text variant='text' text={item.duration} textAlign='center' />
                        </div>
                        {
                            item.is_empty ?
                                <div className='m-t-18'>
                                    <Text variant='meta' text='Empty Leg' textAlign='center' />
                                </div>
                            :
                                null
                        }
                    </div>
                </Grid>
                <Grid item xs={4} md={2}>
                    <div className='c-f a-c j-c h-100'>
                        <div className='r-f j-c'>
                            <FlightLandRoundedIcon />
                        </div>
                        <div className='m-t-12'>
                            <Text variant='text' text={item.destination_place} textAlign='center' />
                        </div>
                        {
                            screenWidth < 960 ?
                                <div className='m-t-12'>
                                    <Text variant='text' text={item.destination_date} textAlign='center'  />
                                    <Text variant='text' text={item.destination_time} textAlign='center'  />
                                </div>
                            :
                                null
                        }
                        
                    </div>
                </Grid>
                {
                    screenWidth >= 960 ?
                        <Grid item xs={2}>
                            <div className='c-f a-c j-c h-100'>
                                <Text variant='text' text={item.destination_date} textAlign='center'  />
                                <Text variant='text' text={item.destination_time} textAlign='center'  />
                            </div>                   
                        </Grid>
                    :
                        null
                }
               
            </Grid>
        </div>
    );
}

export default InfoItem;
