import React from 'react';
import Checkbox from '@material-ui/core/Checkbox';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import { Text } from '../../../../components';


const HotelClass = () => {
    return (
        <div className='m-t-18'>
            <Text text='Hotel Class' variant='sideheading' />
            <div>
                <FormControlLabel
                    value="end"
                    control={<Checkbox color="primary" />}
                    label="5 Star"
                    labelPlacement="end"
                />
            </div>
            <div>
                <FormControlLabel
                    value="end"
                    control={<Checkbox color="primary" />}
                    label="4 Star"
                    labelPlacement="end"
                />
            </div>
            <div>
                <FormControlLabel
                    value="end"
                    control={<Checkbox color="primary" />}
                    label="3 Star"
                    labelPlacement="end"
                />
            </div>
        </div>
    );
}


export default HotelClass;
