import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
// import Chip from '../../../../../../components/chip';
import Text from '../../../../../../components/text';


const useStyles = makeStyles((theme) => ({
    container: {
        padding: theme.spacing(1.5),
        border: `1px solid #cecece`,
        borderRadius: 2,
    },
    photo: {
        height: 160,
        width: 240,
        borderRadius:2,
        overflow: 'hidden',
    },
    flightLine: {
        height: 1,
        width: '100%',
        backgroundColor: '#cecece',
    }
}));


function Activity(props){
    const classes = useStyles();
    const { activity } = props;
    return (
        <div className={classes.container}>
            <div className='r-f'>
                <div className='m-r-12'>
                    <div className={classes.photo}>
                        <img src={activity.photo} alt={activity.title} />
                    </div> 
                </div>
                <div className='f-g-1'>
                    <div className='c-f w-100 h-100'>
                        <div className='r-f a-c'>
                            <div className='m-r-12 f-g-1'>
                                <Text text={activity.title} variant='title' />
                                <Text text={`Activity in ${activity.location}`} variant='meta' />
                            </div>
                        </div>
                        <div className='f-g-1' />
                        <div className='m-t-12'>
                            <div className='r-f a-c'>
                                <div className='r-f a-c m-r-12'>
                                    <Text text={'Duration: '} variant='meta' />
                                    <Text text={`${activity.duration}`} variant='text' />
                                </div>
                                <div className='m-r-12 r-f a-c'>
                                    <Text text={'Suitable for: '} variant='meta' />
                                    <Text text={`Adult, Child, Infant, Teens, Senior`} variant='meta' />
                                </div>
                                {/* <div className=''>
                                    <Text text={'Transfers'} variant='meta' />
                                    <Text text={`Shared Transfers`} variant='text' />
                                </div> */}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            
        </div>
    );
}

export default Activity;
