function SearchHotels(props){
    const [hotelList, setHotelList] = useState([]);
    const [isLoading, setLoading] = useState(true);
    const {query} = QueryString.parse(props.location.search); 
    useEffect(() => {
        window.scrollTo(0, 0);
        setTimeout(() => {
            setHotelList(HOTELS);
            setLoading(false);
        }, 2000);
    }, []);

    useEffect(() => {
        console.log(query);
        setLoading(true);
        setTimeout(() => {
            setHotelList(HOTELS);
            setLoading(false);
        }, 2000);
    }, [query])
    return (
        <div style={{marginTop: 80}}>
            <Header />
            <div className='container'>
                <Grid container spacing={2} className='f-g-1'>
                    <Grid item xs={12} md={3}>
                        <FilterMenu />
                    </Grid>
                    <Grid item xs={12} md={9}>
                        <HotelItemList itemList={hotelList} isLoading={isLoading} />
                    </Grid>
                </Grid>
            </div>
        </div>
    );
}


export default SearchHotels;
