import React from 'react';
import CabForm from './cab';
import Flights from './flights';
import HotelForm from './hotels';
import HolidayForm from './holidays';


function TabPanel(props){
    const { tab } = props;
    return (
        <div>
            {
                tab === 0 ?
                    <HolidayForm type='holiday' />
                :
                    tab === 1 ?
                        <HotelForm />
                    :
                        tab === 2 ?
                            <Flights />
                        :                            
                            // tab === 3 ?
                                <CabForm />
                            // :
                            //     <HolidayForm type='tour' />
            }
        </div>
    );
}


export default TabPanel;
