function Hotel(props){
    const [isLoading, setLoading] = useState(true);
    const [hotel, setHotel] = useState(null);
    
    useEffect(() => {
        window.scrollTo(0, 0);
    }, []);

    const { slug } = props.match.params;
    useEffect(() => {
        setLoading(true);
        axios({
            method: 'GET',
            url: `http://192.168.43.226:8000/hotel/5/`,
        }).then((res) => {
            console.log(res.data);
            setHotel(res.data);
            setLoading(false);
        }).catch((e) => {
            setHotel(HOTEL);
            setLoading(false);
        });
    }, [slug])
    return (
        <div>
            <div className='container'>
                <div style={{marginTop: 76}}>
                    <Info 
                        isLoading={isLoading} 
                        hotel={hotel}
                    />
                </div>
                <Footer />
            </div>
        </div>  
    );
}


export default Hotel;