import React from 'react';
import MaterialDialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';


function Dialog(props){
    return (
        <MaterialDialog
            open={props.open}
            onClose={props.onClose}
            ariaLabelledby={props.name ? props.name : 'simple-dialog-title'}
        >
            {
                props.title ?
                    <DialogTitle 
                        id={
                            props.name ? 
                                props.name 
                            : 
                                "simple-dialog-title"
                        }
                    >
                        {props.title}
                    </DialogTitle>
                :
                    null
            }
            {
                props.children
            }
        </MaterialDialog>
    );
}

export default Dialog;
