import React, { useState, useEffect } from 'react';
import { useLocation, Redirect } from 'react-router-dom';
import AutoComplete from '../../../../../components/input/autocomplete';
import DateInput from '../../../../../components/input/dateInput';
import SelectInput from '../../../../../components/input/selectInput';
import SliderInput from '../../../../../components/input/sliderInput';
import Text from '../../../../../components/text';


const queryString = require('query-string');


function HolidaysForm(props){
    const location = useLocation();
    const queryStr = queryString.parse(location.search);
    const query = JSON.parse(queryStr.query);

    const [prices, setPrices] = useState([1000, 4000]);
    const [duration, setDuration] = useState([3, 12]);

    const [url, setUrl] = useState('');
    const [isRedirect, setRedirect] = useState(false); 


    const onChange = (name, value) => {
        let newQuery = Object.assign({...query});
        newQuery[name] = value;
        setUrl(`/search/holidays?query=${JSON.stringify(newQuery)}`);
        setRedirect(true);
        // if (name === 'departure' || name === 'return') {
        //     console.log('I am in...\n');
        //     newQuery[name] = `${value.getMonth()}/${value.getDate()}/${value.getYear()+1900}`;
        // } else {
        //     newQuery[name] = value;
        // }
        // let url = `/search/holidays?leaving_from=${newQuery.leaving_from}&destination=${newQuery.destination}&departure=${newQuery.departure}&return=${newQuery.return}`;
        // setUrl(url);
        // if (name === 'departure' || name === 'return'){
        //     if (queryStr.departure !== newQuery.departure || queryStr.return !== newQuery.return) {
        //         setRedirect(true);
        //     }
        // } else {
        //     setRedirect(true);
        // }
    }

    useEffect(() => {
        if (isRedirect){
            setRedirect(false);
        }        
    }, [isRedirect]);

    if (isRedirect && url) {
        return(
            <Redirect to={url} />
        );
    }
    return (
        <div>
            <div className='p-tb-12'>
                <AutoComplete 
                    label='Leaving From'
                    name='from'
                    value={query.leaving_from}
                    onChange={(event, newValue) => onChange('leaving_from', newValue)}
                />
            </div>
            <div className='p-tb-12'>
                <AutoComplete 
                    label='Going to'
                    name='to'
                    value={query.destination}
                    onChange={(event, newValue) => onChange('destination', newValue)}
                />
            </div>
            <div className='p-tb-12'>
                <DateInput 
                    label='Departure date'
                    onChange={(date)  => onChange('departure', date)}
                    value={query.departure}
                />
            </div>
            <div className='p-tb-12'>
                <DateInput 
                    label='Return date'
                    onChange={(date) => onChange('return', date)}
                    value={query.return}
                />
            </div>
            <div className='p-tb-12'>
                <Text text='Preferences' variant='title' />
            </div>
            <div className='p-tb-12'>
                <SelectInput 
                    label='Going With'
                    name='going_with'
                    options={['Friends', 'Couples', 'Families', 'Solo', 'Senior Couples']}
                    onChange={(event) => onChange('going_with', event.target.value)}
                    value={query.going_with}
                />
            </div>
            <div className='p-tb-12'>
                <SliderInput
                    label='Price per person'
                    name='price'
                    value={prices}
                    onChange={(event, value) =>{console.log(value); setPrices(value)}}
                    min={1000}
                    max={4000}
                />
            </div>
            <div className='p-tb-12'>
                <SliderInput
                    label='Duration'
                    name='duration'
                    value={duration}
                    onChange={(event, value) => setDuration(value)}
                    min={3}
                    max={12}
                />
            </div>
        </div>
    );
}


export default HolidaysForm;
