import React, { useState } from 'react';
import Menu from '@material-ui/core/Menu';
import { makeStyles } from '@material-ui/core/styles';
import TextInput from '../../../../../../components/input/textInput';
import Text from '../../../../../../components/text'
import { AddRounded, RemoveRounded } from '@material-ui/icons';
import { CHANGE } from './FlightReducer'


const useStyles = makeStyles((theme) => ({
    text: {
        fontSize: theme.typography.fontSize + 2,
        fontFamily: theme.typography.fontFamily,
        fontWeight: theme.typography.fontWeightMedium,
        padding: '8px 16px',
        boxShadow: theme.shadows[3],
        backgroundColor: theme.palette.primary.main,
        color: theme.palette.primary.contrastText,
        borderRadius: 2
    },
    icon: {
        color: 'rgb(62, 62, 62)',
        cursor: 'pointer',
    },
    classRow: {
        boxShadow: theme.shadows[2],
        borderRadius: 2,
    },
    item: {
        padding: '8px 20px',
        fontSize: theme.typography.fontSize,
        cursor: 'pointer',
    },
    active: {
        backgroundColor: theme.palette.primary.main,
        color: '#fff',
        boxShadow: theme.shadows[2],
    }
}))


function PassengerDetails(props){
    const [anchorEl, setAnchorEl] = useState(null);
    const classes = useStyles();
    const { state, dispatch } = props;
    return (
        <div>
            <div onClick={(event) => setAnchorEl(event.currentTarget)}>
                <TextInput 
                    label='Travellers & Class'
                    name='details'
                    value={`${state.adults + state.infants + state.childrens} Travellers - ${state.class} class`}
                />
            </div>
            <Menu
                anchorEl={anchorEl}
                open={Boolean(anchorEl)}
                keepMounted={true}
                onClose={() => setAnchorEl(null)}
            >
                <div className='p-12' style={{outline: 'none'}}>
                    <div className=''>
                        <div className='p-12'>
                            <Text text='Adults (12 Years+)' variant='meta'  />
                            <div className='r-f a-c m-t-12'>
                                <RemoveRounded fontSize='small' className={classes.icon}  onClick={() => dispatch({
                                    type: CHANGE, name: 'adults', value: state.adults - 1
                                })} />
                                <div className='m-lr-12'>
                                    <div className={classes.text}>{state.adults}</div>
                                </div>
                                <AddRounded fontSize='small' className={classes.icon} onClick={() => dispatch({
                                    type: CHANGE, name: 'adults', value: state.adults + 1
                                })} />                                
                            </div>
                        </div>
                        <div className='p-12 m-lr-12'>
                            <Text text='Childrens (2-12 Years)' variant='meta' />
                            <div className='r-f a-c m-t-12'>
                                <RemoveRounded fontSize='small' className={classes.icon} onClick={() => dispatch({
                                    type: CHANGE, name: 'childrens', value: state.childrens - 1
                                })} />
                                <div className='m-lr-12'>
                                    <div className={classes.text}>{state.childrens}</div>
                                </div>
                                <AddRounded fontSize='small' className={classes.icon} onClick={() => dispatch({
                                    type: CHANGE, name: 'childrens', value: state.childrens + 1
                                })} />                                
                            </div>
                        </div>
                        <div className='p-12 m-lr-12'>
                            <Text text='Infants (0-2 Years)' variant='meta' />
                            <div className='r-f a-c m-t-12'>
                                <RemoveRounded fontSize='small' className={classes.icon} onClick={() => dispatch({
                                    type: CHANGE, name: 'infants', value: state.infants - 1
                                })}  />
                                <div className='m-lr-12'>
                                    <div className={classes.text}>{state.infants}</div>
                                </div>
                                <AddRounded fontSize='small' className={classes.icon} onClick={() => dispatch({
                                    type: CHANGE, name: 'infants', value: state.infants + 1
                                })} />                                
                            </div>
                        </div>
                    </div>
                    <div className='r-f a-c'>
                        <div className='p-12'>
                            <Text text='Travel Class' variant='meta' />
                            <div className='m-t-12'>
                                <div className='r-f'>
                                    <div className={classes.classRow}>
                                        <div className='r-f a-c'>
                                            <div className={`${classes.item} ${state.class === 'economy' ? classes.active : ''}`} onClick={() => dispatch({type: CHANGE, name: 'class', value: 'economy'})}>
                                                Economy
                                            </div>
                                            <div className={`${classes.item} ${state.class === 'premium' ? classes.active : ''}`} onClick={() => dispatch({type: CHANGE, name: 'class', value: 'premium'})}>
                                                Premium
                                            </div>
                                            <div className={`${classes.item} ${state.class === 'business' ? classes.active : ''}`} onClick={() => dispatch({type: CHANGE, name: 'class', value: 'business'})}>
                                                Business
                                            </div>
                                        </div>
                                    
                                    </div>
                                </div>
                                
                            </div>
                            
                        </div>
                    </div>
                </div>
                
            </Menu>            
        </div>
    );
}

export default PassengerDetails;
