import React, { useState } from 'react';
import Slider from '@material-ui/core/Slider';
import Text from '../../text';

function valuetext(value) {
    return `${value}`;
}

function SliderInput(props){
    const [values, setValues] = useState([props.min || 0, props.max || 100]);
    return(
        <div>
            <Text text={props.label} variant='meta' />
            <div className='p-lr-8'>
                <Slider 
                    value={values}
                    valueLabelDisplay="auto"
                    aria-labelledby="range-slider"
                    getAriaValueText={valuetext}
                    onChange={(event, newValue) => setValues(newValue)}
                    onChangeCommitted={props.onChange}
                    min={props.min}
                    max={props.max}
                />
            </div>
            <div className='p-lr-8'>
                <div className='r-f a-c'>
                    <Text text={props.value[0]} variant='text' />
                    <div className='f-g-1' />
                    <Text text={props.value[1]} variant='text' />
                </div>
            </div>
        </div>
    );
}


export default SliderInput;