import React from 'react';
import { Typography, makeStyles } from '@material-ui/core';


const useStyles = makeStyles((theme) => ({
    heading: {
        fontSize: theme.typography.fontSize + 12,
        fontWeight: theme.typography.fontWeightMedium,
    },
    meta: {
        fontSize: theme.typography.fontSize - 1,
        fontWeight: theme.typography.fontWeightRegular,
        color: theme.palette.type === 'dark' ? '#f1f1f1' : '#4a4a4a',
    },
    title: {
        fontSize: theme.typography.fontSize,
        fontWeight: theme.typography.fontWeightMedium,
    },
    text: {
        fontSize: theme.typography.fontSize,
        fontWeight: theme.typography.fontWeightRegular,
    },
    sideHeading: {
        fontSize: theme.typography.fontSize + 2,
        fontWeight: theme.typography.fontWeightMedium,
    },
    subHeading: {
        fontSize: theme.typography.fontSize + 6,
        fontWeight: theme.typography.fontWeightMedium, 
    },
    strike: {
        textDecorationLine: 'line-through',
    },
    center: {
        textAlign: 'center',
    },
    danger: {
        color: '#eb2026 !important',
    },
    h5: {
        fontSize: theme.typography.fontSize + 6,
        fontWeight: theme.typography.fontWeightNormal,
    },
    h6: {
        fontSize: theme.typography.fontSize + 4,
        fontWeight: theme.typography.fontWeightNormal,
    },
    preLine: {
        whiteSpace: 'pre-line',
    }
}));


function Text(props){
    const classes = useStyles();
    const { variant } = props;
    return (
        <Typography 
            color={variant === 'meta' ? 'textSecondary' : props.color === 'primary' ? 'primary' : props.color === 'inherit' ?  'inherit' : props.color === 'secondary' ? 'secondary' : 'textPrimary'} 
            className={`${
                variant === 'title' ?
                    classes.title
                :
                    variant === 'text' ?
                        classes.text
                    :
                        variant === 'meta' ? 
                            classes.meta 
                        : 
                            variant === 'sideheading' ?
                                classes.sideHeading
                            :
                                variant === 'subheading' ?
                                    classes.subHeading
                                :
                                    variant === 'h5' ?
                                        classes.h5
                                    :
                                        variant === 'h6' ?
                                            classes.h6
                                        :
                                            classes.heading
            } ${props.strike ? classes.strike : ''} ${props.textAlign === 'center' ? classes.center : ''} 
             ${props.color === 'danger' ? classes.danger : ''}
             ${props.whitespace === 'pre-line' ? classes.preLine : ''}
            `}
        >
            {props.text}
        </Typography>
    );
}


export default Text;