import React from 'react';
import useScreen from '../../../../hooks/useScreen';
import Cardlist from '../../../../components/cardlist';
import Itemlist from '../../../../components/itemlist';
import HotelItem from './HotelItem';
import LoadingItem from '../../holidays/holidayItem/LoadingItem';


function HotelItemList(props){
    const { screenWidth } = useScreen();
    let list = [], loadingList=[];
    for(let i = 0; i < 10; i++) {
        list.push(<HotelItem key={i}/>);
    }
    for (let i = 0; i < 10; i++) {
        loadingList.push(<LoadingItem key={i} />);
    }
    const { itemList } = props;
    return (
        <div>
            {
                props.isLoading ?
                    loadingList
                :
                    <React.Fragment>
                        {
                            screenWidth < 950 ?
                                <Cardlist itemList={itemList} />
                            :
                                <Itemlist itemList={itemList} />
                        }
                    </React.Fragment>
            }
        </div>
    );
}


export default HotelItemList;
