import React, { useEffect, useState } from 'react';
import { Redirect } from 'react-router-dom';
import Card from '@material-ui/core/Card';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import Button from '../../../components/button';
import SelectInput from '../../../components/input/selectInput';
import Text from '../../../components/text';
import SearchIcon from '@material-ui/icons/Search';


const useStyles = makeStyles((theme) => ({
    root: {
        marginTop: 12,
        zIndex: 10,
        position: 'relative',
    },
    container: {
        padding: 32,
    }
}));


function Search(props){
    const [portfolio, setPortfolio] = useState('Startup');
    const [category, setCategory] = useState('');
    const [isRedirect, setRedirect] = useState(false);
    const classes = useStyles();

    const onSubmit = () => {
        if (portfolio ) {
            setRedirect(true);
        }
    }

    useEffect(() => {
        if (isRedirect) {
            setRedirect(false);
        }
    }, [isRedirect])

    if (isRedirect) {
        return(
            <Redirect to={`/invest?portfolio=${portfolio}${category ? `&category=${category}` : ''}`} />
        );
    }
    return (
        <div className={classes.root}>
            <Card>
                <div className={classes.container}>
                    <div className=''>
                        <Text 
                            color='primary' 
                            text='Find a Portfolio or Company to invest' 
                            variant='heading' 
                            textAlign='center' 
                        /> 
                    </div>
                    <div className='m-t-18 p-tb-12' />
                    <Grid container spacing={2}>
                        <Grid item xs={12} md={5}>
                            <SelectInput
                                label='Portfolio'
                                options={[
                                    'Startup', 'Luxury Hotels', 'Resorts', 'Restaurants', 'Private Homes',
                                ]}
                                value={portfolio}
                                onChange={(event) => setPortfolio(event.target.value)}
                            />
                        </Grid>
                        <Grid item xs={12} md={5}>
                            <SelectInput 
                                label='Category'
                                options={[
                                    'Agriculture',
                                    'Auto Mobiles',
                                    'IT and Software',
                                    'Non Renewable Energy',                            
                                ]}
                                value={category}
                                onChange={(event) => setCategory(event.target.value)}
                            />
                        </Grid>
                        <Grid item xs={12} md={2}>
                            <div className='r-f a-c h-100'>
                                <Button icon={<SearchIcon />} text='Search' onClick={onSubmit} />
                            </div>                           
                        </Grid>
                    </Grid>
                    <div className='m-t-18'/>
                </div>
            </Card>            
        </div>
    );
}

export default Search;
