import React from 'react';
import { DATA } from './Data'

function Svg(props){
    const data = DATA[props.icon] || {};
    return(
        <svg 
            viewBox={data.viewBox} 
            height={data.size[props.size].height} 
            width={data.size[props.size].width}
        >
            <path d='' />
            {
                Array.isArray(data.path) ?
                    data.path.length ?
                        data.path.map((d, index) => (
                            <path d={d} key={index} />
                        ))
                    :
                        null
                :
                    <path d={data.path} />
            }
        </svg>
    );
}


export default Svg;