import React from 'react';
import FlightItem from './FlightItem';
import LoadingItem from '../../holidays/holidayItem/LoadingItem';
import FlightCard from './FlightCard';
import Grid from '@material-ui/core/Grid';
import useScreen from '../../../../hooks/useScreen';


function FlightItemList(props){
    const { screenWidth } = useScreen();
    const { flightList } = props;
    let loadingList = [];
    if (props.isLoaging || props.isMoreLoading) {
        for (let i = 0; i < 10; i++) {
            loadingList.push(<LoadingItem key={i} />);
        }
    }
    return (
        <div>
            {
                props.isLoading ?
                    loadingList
                :
                    <React.Fragment>
                        {
                            screenWidth < 950 ?
                                <Grid container spacing={2}>
                                    {
                                        flightList.map((flight, index) => (
                                            <Grid item xs={12} sm={6} md={4} key={index} >
                                                <FlightCard flight={flight} />
                                            </Grid>
                                           
                                        ))
                                    }
                                </Grid>
                            :
                                <React.Fragment>
                                    {
                                        flightList.map((flight, index) => (
                                            <FlightItem flight={flight} key={index} />
                                        ))
                                    }
                                </React.Fragment>
                        }
                    </React.Fragment>
                   
            }
        </div>
    );
}

export default FlightItemList;
