import React from 'react';
import { makeStyles } from '@material-ui/core';


const useStyles = makeStyles((theme) => ({
    logoText: {
        fontSize: 24,
        fontWeight: 'bold',
        fontFamily: 'inherit',
        // color: '#02b6f0',
        color: theme.palette.primary.main,
    }
}));


function Logo(props){
    const classes = useStyles();
    return (
        <div className={classes.logoText}>
            <div>CWB Luxury</div>
        </div>
    );
}

export default Logo;
