import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
// import Check from '@material-ui/icons/CheckRounded';
import Breakfast from '@material-ui/icons/FreeBreakfastRounded';
import Dinner from '@material-ui/icons/FastfoodRounded';
import Button from '../../../../../../components/button';
import Text from '../../../../../../components/text';


const useStyles = makeStyles((theme) => ({
    container: {
        border: '#cecece 1px solid',
        borderRadius: 2,
        '&:hover': {
            borderColor: theme.palette.primary.main,
        }
    },
    content: {
        padding: theme.spacing(1.5),
        width: 'calc(100% - 168px)',
        [theme.breakpoints.down('xs')]: {
            width: '100%',
        }
    },
    priceContainer: {
        padding: theme.spacing(1.5),
        width: 168,
        backgroundColor: 'rgba(2, 182, 240, 0.16)',
        [theme.breakpoints.down('xs')]: {
            width: '100%',
        }
    },
    icon: {
        height: 20,
        width: 20,
        marginRight: 8,
    }
}));


function Option(props){
    const classes = useStyles();
    const { index, screenWidth }= props; 
    return (
        <div className={classes.container}>
            <div className='r-f-w'>
                <div className={classes.content}>
                    <Text text={`Option ${index + 1}`} variant='title' />
                    <Text text='Free cancalation before 5th Aug' variant='meta' />

                    <div className='m-t-12'>
                        <div className='r-f a-c'>
                            <div className={classes.icon}>
                                <Breakfast fontSize='small' color='secondary' />
                            </div>
                            <Text text='Breakfast included' variant='meta' />
                        </div>
                        <div className='m-t-8 r-f a-c'>
                            <div className={classes.icon}>
                                <Dinner fontSize='small' color='secondary' />
                            </div>
                            <Text text='Meal or Dinner included' variant='meta' />
                        </div>
                    </div>
                </div>
                <div className={classes.priceContainer}>
                    {
                        screenWidth > 600 ?
                            <React.Fragment>
                                <div>
                                    <Text text='$400' variant='heading' color='primary' textAlign='center' />
                                    <Text text='per night' variant='meta'  textAlign='center'  />
                                </div>
                                <div className='m-t-12 r-f j-c'>
                                    <Button text='Reserve Now' />
                                </div>
                            </React.Fragment>
                        :
                            <div className='r-f a-c j-c'>
                                <div className=''>
                                    <div className='r-f a-c'>
                                        <div>
                                            <Text text='$400' variant='heading' color='primary' textAlign='center' />
                                        </div>
                                        <div className='m-l-6'>
                                            <Text text='per night' variant='meta'  textAlign='center'  />
                                        </div>
                                    </div>
                                </div>
                                <div className='m-l-12'>
                                    <Button text='Reserve Now' />
                                </div>
                            </div>
                    }
                    
                </div>
            </div>
        </div>
    );
}

export default Option;
