import React from 'react';
import { makeStyles } from '@material-ui/core/styles';


const useStyles = makeStyles((theme) => ({
    logo: {
        marginLeft: 12,
        fontSize: 20,
        fontWeight: '500',
        color: '#fff',
    }
}));


function Logo(props){
    const classes = useStyles();
    return (
        <div>
            <div className='r-f a-c'>
                <div className={classes.logo}>CWB Shop</div>
            </div>
        </div>
    );
}


export default Logo;