import React from 'react';
import HotelItem from './HotelItem';


function HotelItemList(props){
    let list = [];
    for(let i =0; i< 20; i++) {
        list.push(<HotelItem />);
    }
    return(
        <div>
            {
                list
            }
        </div>
    );
}


export default HotelItemList;