import React, { useReducer, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import { DateInput, Button, AutoComplete } from '../../../../../components';
import PassengerDetails from '../flights/form/PassengerDetails';
import { CHANGE, ERROR, REMOVE_ERROR, hotelReducer, initialState } from './HotelReducer';
import SearchIcon from '@material-ui/icons/Search';


const HOTELS = ['New Delhi - India', 'Thimpu Bhutan', 'Bangalore - India', 'Chicago - USA', 'Durban - South Africa', 'London - UK'];


function HotelForm(props){
    const [state, dispatch] = useReducer(hotelReducer, initialState);
    const history = useHistory();
    const { error } = state;
    const validate = () => {
        let flag = true;
        if (!state.destination) {
            dispatch({type: ERROR, name: 'destination', value: 'Please enter a destination'});
            flag = false;
        }
        if (!state.check_in) {
            dispatch({
                type: ERROR,
                name: 'check_in',
                value: 'Please enter a check in date',
            });
        }
        if (!state.check_out) {
            dispatch({
                type: ERROR,
                name: 'check_out',
                value: 'Please enter a check out date',
            });
        }
        return flag;
    }

    const onSubmit = () => {
        if (validate()) {
            let query = {};
            query.destination = state.destination;
            query.check_in = state.check_in;
            query.check_out = state.check_out;
            query.adults = state.adults;
            query.childrens = state.childrens;
            query.infants = state.infants;
            // history.push(`/search/hotels?query=${JSON.stringify(query)}`);
            console.log(history);
        } else {
            console.log(state);
        }
    }

    let checkoutMinDate = state.check_in ? new Date(state.check_in) : new Date();
    checkoutMinDate.setDate(checkoutMinDate.getDate() + 1);

    useEffect(() => {
        console.log(state.destination);
        if(state.destination && error.destination) {
            console.log('I am in...\n');
            dispatch({type: REMOVE_ERROR, name: 'destination'});
        }
    }, [state.destination, error.destination])

    useEffect(() => {
        if (state.check_in && error.check_in) {
            dispatch({type: REMOVE_ERROR, name: 'check_in'});
        }
    }, [state.check_in, error.check_in]);

    useEffect(() => {
        if (state.check_out && error.check_out) {
            dispatch({type: REMOVE_ERROR, name: 'check_out'});
        }
    }, [state.check_out, error.check_out]);
    
    return (
        <div>
            <div>
                <Grid container spacing={2}>
                    <Grid item xs={12} sm={6} md={3} >
                        <AutoComplete 
                            name='destination'
                            value={state.destination}
                            label='Destination or Hotel'
                            onChange={(event, value) => dispatch({type: CHANGE, name: 'destination', value: value})}
                            error={error.destination}
                            options={HOTELS}
                        />                    
                    </Grid>
                    <Grid item xs={12} sm={6} md={3} >
                        <DateInput 
                            label='Check-in'
                            name='check_in'
                            value={state.check_in}
                            onChange={(date) => dispatch({type: CHANGE, name: 'check_in', value: date})}
                            error={error.check_in}
                        />
                    </Grid>
                    <Grid item xs={12} sm={6} md={3}>
                        <DateInput 
                            label='Check out'
                            name='check_out'
                            value={state.check_out}
                            onChange={(date) => dispatch({type: CHANGE, name: 'check_out', value: date})}
                            error={error.check_out}
                            minDate={checkoutMinDate}
                        />
                    </Grid>
                    <Grid item xs={12} sm={6} md={3}>
                        <PassengerDetails 
                            state={state}
                            dispatch={dispatch}
                        />
                    </Grid>
                </Grid>
            </div>
            <div className='m-t-6'>
                <Grid container spacing={2} justify='flex-end'>
                    <Grid item>
                        <Button icon={<SearchIcon /> } text='Search' onClick={onSubmit} />
                    </Grid>                    
                </Grid>
            </div>
        </div>
    );
}


export default HotelForm;