import React, { useEffect, useReducer } from 'react';
import { useHistory } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import SearchIcon from '@material-ui/icons/Search';
import { ADD_ROUTE, CHANGE, initialState, multicityReducer, REMOVE_ERROR } from './MulticityReducer';
import { DateInput, Button, AutoComplete } from '../../../../../../components';
import PassengerDetails from '../form/PassengerDetails';

const ERROR = 'ERROR';

function Multicity(props){
    const [state, dispatch] = useReducer(multicityReducer, initialState);
    const history = useHistory();

    const validate = () => {
        let flag = true;
        for (let i = 0; i < state.length;i++) {            
            if (!state[0].from) {
                dispatch({type: ERROR, index: i, name: 'from', value: 'Please enter a leaving place'});
                if (flag) {
                    flag = false;
                }
            }
            if (!state[0].to) {
                dispatch({type: ERROR, index: i, name: 'to', value: 'Please enter a destination'});
                if (flag) {
                    flag = false;
                }
            }
            if (!state[0].departure) {
                dispatch({type: ERROR, index: i, name: 'departure', value: 'Please enter a departure date'});
                if (flag) {
                    flag = false;
                }
            }
            
        }
        return flag;
    }
    const onSubmit = () => {
        if (validate()) {
            console.log(history);
            // history.push(`/search/flights?trip=multicity&query=${JSON.stringify(state)}`);
        }
    }

    useEffect(()=> {
        for (let i = 0; i < state.length; i++) {
            // if(state[i])
            Object.keys(state[i]).forEach((key) => {
                if (state[i][key] && state[i].error[key]) {
                    dispatch({type: REMOVE_ERROR, index: i, name: key});
                }
            });
        }
    }, [state])
    return (
        <div className=''>
            <div className=''>
                {
                    state.length > 0 ?
                        state.map((currentState, index) => (
                            <div className='' style={{marginBottom: 16}} key={index}>
                                <Grid container spacing={2} >
                                    <Grid item xs={6} sm={3}>
                                        <AutoComplete 
                                            name='from'
                                            value={currentState.from}
                                            label='Leaving From'
                                            onChange={(event, value) => dispatch({type: CHANGE, index: index, name: 'from', value: value})}
                                            error={currentState.error.from}
                                        />      
                                    </Grid> 
                                    <Grid item xs={6} sm={3}>
                                        <AutoComplete
                                            name='to'
                                            value={currentState.to}
                                            label='Going to'
                                            onChange={(event, value) => dispatch({type: CHANGE, index: index, name: 'to', value: value})}
                                            error={currentState.error.to}
                                        />
                                    </Grid> 
                                    <Grid item xs={6} sm={3}>
                                        <DateInput 
                                            label='Departure Date'
                                            name='departure'
                                            value={currentState.departure}
                                            onChange={
                                                (date) => dispatch({type: CHANGE, index: index, name: 'departure', value: date})
                                            }
                                            error={currentState.error.departure}
                                        />
                                    </Grid> 
                                    <Grid item xs={6} sm={3}>
                                    {
                                            index === 0 ?
                                                <PassengerDetails state={currentState} dispatch={dispatch} />
                                            :
                                                index === state.length - 1  && state.length < 3?
                                                    <div className='r-f a-c h-100'>
                                                        <Button 
                                                            text='Add trip' 
                                                            onClick={() => dispatch({type: ADD_ROUTE})} 
                                                            variant='outlined'
                                                        /> 
                                                    </div>
                                                
                                                :
                                                    null
                                    }
                                    </Grid> 
                                </Grid>
                            </div>
                        ))
                    :
                            null
                }
                <Grid container spacing={2} justify='flex-end'>
                    <Grid item>
                        <Button 
                            icon={<SearchIcon />}
                            text='Search' 
                            onClick={onSubmit} 
                        />
                    </Grid>                    
                </Grid>
            </div>
        </div>
    );
}


export default Multicity;
