export const initialState = {
    email: '',
    password: '',
    error: {
        email: null,
        password: null,
    }
};


export const signupState = {
    email: '',
    name: '',
    password: '',
    confirmPassword: '',
    error: {
        email: null,
        name: null,
        password: null,
        confirmPassword: null,
    }
}


export const CHANGE = 'CHANGE';
export const ERROR = 'ERROR';
export const REMOVE_ERROR = 'REMOVE_ERROR';


export function signinReducer(state, action) {
    switch (action.type) {
        case CHANGE: 
            return {...state, [action.name]: action.value};
        case ERROR: 
            return {...state, error: {...state.error, [action.name]: action.value}};
        case REMOVE_ERROR:
            return {...state, error: {...state.error, [action.name]: null}};
        default:
            return state;
    }
}