import React, { useState } from 'react';
import { RadioGroup, FormControl } from '@material-ui/core';
import RadioInput from '../../../../../components/input/radioInput';
import RoundTrip from './roundtrip';
import Multicity from './multicity';
import Singletrip from './singletrip';



function Flights(props){
    const [trip, setTrip] = useState('single-trip');
    return (
        <div className='' style={{marginTop: -16}}>
            <FormControl component='fieldset'>
                <RadioGroup 
                    row 
                    aria-label="position" 
                    name="position" 
                    value={trip}
                    onChange={(event) => setTrip(event.target.value)}
                >
                    <RadioInput 
                        label='Single Trip'
                        value='single-trip'
                    />
                    <RadioInput 
                        label='Round Trip'
                        value='round-trip'
                    />
                    {/* <RadioInput 
                        label='Multicity'
                        value='multicity'
                    /> */}
                </RadioGroup>
            </FormControl>
            <div className='m-t-12'>
                {
                    trip === 'single-trip' ?
                        <Singletrip />
                    :
                        trip === 'round-trip' ?
                            <RoundTrip />
                        :
                            <Multicity />
                }
            </div>           
        </div>
    );
}


export default Flights;