import React from 'react';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Button from '../../../components/button';



const useStyles = makeStyles((theme) => ({
    container: {
        paddingTop: 72,
        width: '100%',
        height: 'auto',
        padding: '0 16px'
        // minHeight: '500px',
        // backgroundColor: theme.palette.secondary.light,
    },
    content: {
        padding: '12px 12px 32px 32px',
    },
    title: {
        fontSize: theme.typography.fontSize + 14,
        fontWeight: theme.typography.fontWeightMedium,
        fontFamily: theme.typography.fontFamily,
        lineHeight: 1.25,
        color: '#1a1a1a',
        textTransform: 'uppercase',
    },
    meta: {
        fontSize: theme.typography.fontSize,
        fontWeight: theme.typography.fontWeightNormal,
        fontFamily: theme.typography.fontFamily,
        lineHeight: 1.5,
        color: '#4a4a4a'
    },
    text: {
        marginTop: 12,
        fontSize: theme.typography.fontSize + 4,
        fontWeight: theme.typography.fontWeightNormal,
        fontFamily: theme.typography.fontFamily,
        lineHeight: 1.5,
        color: '#4a4a4a',
        letterSpacing: 0.001,
    }
}));

function Gallery(props){
    const classes = useStyles();
    return (
        <div>
            <div className={classes.container}>
                <div>
                    <Grid container spacing={2}>
                        <Grid item xs={12} md={6} lg={5}>
                            <div className={classes.content}>
                                <Typography className={classes.title}>It's your time to be investor</Typography>
                                <Typography className={classes.meta}>Equity crowd funding</Typography>
                                <Typography className={classes.text}>
                                    CWB Luxury allows you to have access to dozens of investment opportunities in Startups, Luxury Hotels, Luxury Resorts, Luxury Restorants, Luxury Villas, Private homes and can invest in future businesses as minimum as $50.
                                </Typography>
                                <div className='m-t-18 p-tb-12'>
                                    <div className='r-f a-c'>
                                        <div>
                                            <Button text='Start Investing' />
                                        </div>
                                        <div className='m-l-12'>
                                            <Button text='Get Funding' color='secondary' />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </Grid>
                        <Grid item xs={12} md={6} lg={7}>
                            <div>
                                <iframe width="100%" height="400" src="https://www.youtube.com/embed/dtpMMKJefJk" frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" title='Chorten Wangyel' allowfullscreen></iframe>
                            </div>
                        </Grid>
                    </Grid>
                    
                </div>
            </div>
        </div>
    );
}

export default Gallery;
