import React, { useEffect, useState } from 'react';
import { Redirect, useLocation } from 'react-router-dom';
import MenuItem from '@material-ui/core/MenuItem';
import Checkbox from '@material-ui/core/Checkbox';
import Text from '../../../../../components/text';

const QueryString = require('query-string');


const MENU = [
    {
        text: 'Popularity',
        value: 'popularity',
    },
    {
        text: 'Price low to high',
        value: 'ascending',
    },
    {
        text: 'Price high to low',
        value: 'descending',
    }
];


function Menu(props, ref){
    const location = useLocation();
    const [url, setUrl] = useState(null);

    useEffect(() => {
        if(url) {
            setUrl(null);
        }
    }, [url]);

    const queryStr = QueryString.parse(location.search);
    const onChange = (value) => {
        
        let newUrl = location.pathname;
        if (queryStr.query){
            newUrl = `${newUrl}?query=${queryStr.query}&`;
        } else {
            newUrl = `${newUrl}?`;
        }
        newUrl = `${newUrl}sort=${value}`;
        setUrl(newUrl);
        props.onClose();
    }

    if (url) {
        return(
            <Redirect to={url} />
        );
    }
    const value = queryStr.sort || 'popularity';
    return (
        <div>
            {
                MENU.map((menu, index) => {
                    return(
                        <MenuItem key={index} onClick={() => onChange(menu.value)}>
                            <div className='r-f a-c'>
                                <div>
                                    <Checkbox  
                                        name=''
                                        color='primary'
                                        checked={value === menu.value}
                                    />
                                </div>
                                <div className='' style={{marginRight: 18}}>
                                    <Text variant='meta' text={menu.text} />
                                </div>
                            </div>
                        </MenuItem>
                    );
                })
            }
        </div>
    );
}

export default React.forwardRef(Menu);
