import React from 'react';
// import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import Skeleton from '@material-ui/lab/Skeleton';
import Chip from '../../../../../components/chip';
import Text from '../../../../../components/text';
import ButtonRow from './buttonRow';


const useStyles = makeStyles((theme) => ({
    container: {
        paddingLeft: theme.spacing(2.5),
        width: 'calc(100% - 424px)',
    },
    label: {
        width: 180,
    },
    detail: {
        width: 'calc(100% - 180px)'
    }
}));


function Details(props){
    const classes = useStyles();
    const { isLoading, product } = props; 
    return (
        <div className={classes.container}>
            <div>
                {
                    isLoading ?
                        <Skeleton style={{width: '50%', height: 24}} />
                    :
                        <Text text={product.title} variant='h6' />
                }
                <div className='m-t-6'>
                    <div className='r-f a-c'>
                        <Chip label='4.5/5' color='success' size='small' />
                        <div className='m-l-6'>
                            <Text text='1024 Ratings and 240 Reviews' variant='meta' />
                        </div>
                    </div>
                    <div className='m-t-8'>
                        <div className='r-f a-c'>
                            <Text text='12,189' variant='h2' />
                            <div className='m-l-12'>
                                <Text text='18,750' strike={true} variant='meta' />
                            </div>
                            <div className='m-l-12'>
                                <Text text='34% off' variant='title' color='secondary' />
                            </div>
                        </div>
                    </div>
                    <div className='m-t-12'>
                        <ButtonRow />
                    </div>
                </div>         
            </div>
            <div className='m-t-12 p-tb-12'>
                {
                    isLoading ? 
                        null
                    :
                        <div className='r-f'>
                            <div className={classes.label}>
                                <Text variant='meta' text='Description' />
                            </div>
                            <div className={classes.detail}>
                                <Text text={product.description} variant='meta' whitespace='pre-line' />
                            </div>
                        </div>
                }                       
            </div>
            <div className='m-t-12'>
                <div className=''>
                    {
                        isLoading ? 
                            null
                        :
                            <div>
                                {
                                    product.productdetail_set.map((detail, index) => (
                                        <div className='p-tb-12' key={index}>
                                            <div className='r-f'>
                                                <div className={classes.label}>
                                                    <Text variant='meta' text={detail.label} />
                                                </div>
                                                <div className={classes.detail}>
                                                    <Text variant='meta' text={detail.text} whitespace='pre-line' />
                                                </div>
                                            </div>
                                        </div>
                                    ))
                                }
                            </div>
                    }
                </div>
            </div>
            <div className='m-t-12'>
                {
                    isLoading ? 
                        null
                    :
                        <div>
                            {
                                product.productsize_set.map((size, index) => {
                                    if (index > 0) {
                                        return null;
                                    } else {
                                        return(
                                            <React.Fragment key={index}>
                                                <div className='p-tb-12'>
                                                    <div className='r-f'>
                                                        <div className={classes.label}>
                                                            <Text variant='meta' text='Size' />
                                                        </div>
                                                        <div className={classes.detail}>
                                                            <Text variant='meta' text={`${size.width}X${size.breadth}`} />
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className='p-tb-12' key={index}>
                                                    <div className='r-f'>
                                                        <div className={classes.label}>
                                                            <Text variant='meta' text='Weight' />
                                                        </div>
                                                        <div className={classes.detail}>
                                                            <Text variant='meta' text={`${size.weight} grams`} />
                                                        </div>
                                                    </div>
                                                </div>
                                            </React.Fragment>                                            
                                        );
                                    }
                                })
                            }
                        </div>
                }
            </div>
        </div>
    );
}


export default Details;
