import React from 'react';
import Activity from './activity';
import Break from './break';
import Cab from './cab';
import End from './end';
import Flight from './flight';
import Hotel from './hotel';
import Label from './label';


function Dayplan(props){
    return(
        <div className='m-t-12'>
            <Label label='Day 1' />
            <Break start={true} />
            <Flight 
                flight={{
                    title: 'MG-5',
                    photo: 'https://imgak.mmtcdn.com/air/pwa/dist/assets/img/airline-logos/smSG.gif?v=17',
                    from: {
                        place: 'New Delhi',
                        time: '07:45',
                    },
                    to: {
                        place: 'Dubai',
                        time: '10:10',
                    }

                }}
            />
            <Break />
            <Cab 
                label='Airport to hotel in Dubai'
            />
            <Break label='Check-in to Hotel in Dubai @2 PM' />
            <Hotel 
                hotel={{
                    title: 'Howard Johnson Bur Dubai Hotel - MMT Special',
                    location: 'Bur Dubai, Dubai, UAE',
                    rating: 4.5,
                    check_in : 'SUN, 14 AUG 2020',
                    check_out: 'WED, 17 AUG 2020',
                    room_type: 'Standard Room',
                    includes: 'Breakfast',
                    photo: 'https://r1imghtlak.mmtcdn.com/4c54e3182c3411e8be620a9df65c8753.jpg?&downsize=900:675&crop=900:675;208,0&output-format=jpg',
                }}
            />
            <Break />
            <End />
            <Label label='Day 2' />
            <Break start={true} label='Breakfast at hotel' />
            <Activity 
                activity={{
                    title: 'Book Desert Safari Get 5 Star Dubai City tour Creek Harbor Lunch cruise with transfers COMPLIMENTARY',
                    location: 'Dubai',
                    duration: '3 hrs',
                    photo: 'https://media.tacdn.com/media/attractions-splice-spp-360x240/07/35/dc/59.jpg',
                }}
            />
            <Break />
            <End />
            <Label label='Day 3' />
            <Break start={true} label='Breakfast at hotel' />
            <Cab 
                label='Hotel to Airport in Dubai'
            />
            <Break />
            <Flight 
                flight={{
                    title: 'MG-5',
                    photo: 'https://imgak.mmtcdn.com/air/pwa/dist/assets/img/airline-logos/smSG.gif?v=17',
                    from: {
                        place: 'Dubai',
                        time: '10:10',
                    },
                    to: {
                        place: 'New Delhi',
                        time: '12:45',
                    }

                }}
            />
        </div>
    );
}


export default Dayplan;