import React, { useRef, useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import IconButton from '@material-ui/core/IconButton';
import ChevronRightRoundedIcon from '@material-ui/icons/ChevronRightRounded';
import ChevronLeftRoundedIcon from '@material-ui/icons/ChevronLeftRounded';


const useStyles = makeStyles((theme) => ({
    button:{
        position: 'absolute', 
        top: '50%', 
        backgroundColor: 'rgba(0, 0, 0, 0.5)', 
        color: '#fff', 
        zIndex: 20,
        borderRadius: '50%',
        '&:hover': {
            backgroundColor: 'rgba(0, 0, 0, 0.5)', 
        }
    },
    leftButton: {
        left: 12,
    },
    rightButton: {
        right: 12,
    },
    slider: {
        display: 'flex',
        position: 'relative',
        transition: 'all 350ms linear',
    },
    container: {
        width:'100%',
        overflow: 'hidden',
        position: 'relative',
    },
    image: {
        width: '100%',
        minWidth: '100%',
    }
}));

function Carousel(props){
    const classes = useStyles();
    const containerRef = useRef();
    const [margin, setMargin] = useState(0);
    const [index, setIndex] = useState(0);
    const [style, setStyle] = useState({}); 
    useEffect(() => {
        if (containerRef.current) {
            setStyle({
                width: containerRef.current.offsetWidth,
                minWidth: containerRef.current.offsetWidth,
                height: containerRef.current.offsetWidth * 0.56
            });
        }       
    }, [containerRef])
    const onRightClick = () => {
        let newMargin, newIndex;
        if (index < props.images.length - 1) {
            newMargin = margin - containerRef.current.offsetWidth; 
            newIndex = index + 1;
        } else {
            newMargin = 0;
            newIndex = 0;
        }
        setMargin(newMargin);
        setIndex(newIndex);
    }
    const onLeftClick = () => {
        let newMargin, newIndex;
        if (index > 0) {
            newMargin = margin + containerRef.current.offsetWidth; 
            newIndex = index - 1;
        } else {
            newMargin = -(containerRef.current.offsetWidth * (props.images.length - 1));
            newIndex = props.images.length - 1;
        }
        setMargin(newMargin);
        setIndex(newIndex);
    }
    console.log(props.images);
    return (
        <div className={classes.container} style={style} ref={containerRef}>
            <div className={classes.slider} style={{marginLeft: margin}}>
                {
                    props.images.map((image, index) => (
                        <div className={classes.image} style={style} key={index}>
                            <img src={image.original} alt='' />
                        </div>
                    ))
                }
            </div>
            <IconButton className={`${classes.button} ${classes.leftButton}`} onClick={onLeftClick}>
                <ChevronLeftRoundedIcon />
            </IconButton>
           <IconButton className={`${classes.button} ${classes.rightButton}`} onClick={onRightClick}>
                <ChevronRightRoundedIcon />
            </IconButton>
        </div>
    );
}

export default Carousel;
