import React from 'react';
import Text from '../../../../components/text';


function MapOverview(props){
    return (
        <div>
            <div className='m-t-12'>
                <Text text='Location' variant='title' />
            </div>
            <div className='m-t-12'>
                <div style={{height: 120, width: '100%', backgroundColor: '#cecece'}} />
            </div>
        </div>
    );
}


export default MapOverview;
