import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Check from '@material-ui/icons/CheckRounded';
import Text from '../../../../components/text';


const useStyles = makeStyles((theme) => ({
    item: {
        marginTop: theme.spacing(1.5),
        marginRight: theme.spacing(1.5)
    },
    icon: {
        height: 20,
        width: 20,
        marginRight: 8,
    }
}));


const FACILITIES = {
    'GENERAL SERVICES': ['Doctor on Call', 'Luggage Storage', 'Bellboy Service', 'Facilities for Guests with Disabilities'],
    'COMMON AREA': ['Lawn', 'Reception'],
    'BASIC FACILITIES': ['Free Wi-Fi', 'Free Parking', 'Room Service', 'Power Backup', 'Elevator Lift','Air Conditioning (Room controlled)', 'Swimming Pool (Common Pool)', 'Dry Cleaning Service', 'Smoke Detector', 'Laundry Service', 'Intercom', 'Housekeeping', 'Public Restrooms', 'Telephone (Paid)', 'Attached Bathroom', 'Ironing Service (Limited Pieces of Laundry Free)'],
    'FOOD AND DRINKS': ['Restaurant', 'Bar', 'Cafe', 'Coffee Shop', 'Special Diet Meals'],
    'SAFETY AND SECURITY': ['Safe', 'Fire Extinguishers', 'Safety and Security', 'Security']
};


function Facilities(props){
    const classes = useStyles();
    return (
        <div className='m-t-18'>
            <div>
                <Text text='Facilities at Royal Orchid' variant='sideheading' />
            </div>
            <div>
                {
                    Object.keys(FACILITIES).map((key, index) => (
                        <div className='m-t-18' key={index}>
                            <Text text={key} variant='text' />
                            <div className='r-f-w a-c'>
                                {
                                    FACILITIES[key].map((item, index) => (
                                        <div className={classes.item} key={index}>
                                            <div className='r-f a-c'>
                                                <div className={classes.icon}>
                                                    <Check fontSize='small' color='secondary' />
                                                </div>
                                                <div>
                                                    <Text text={item} variant='meta' />
                                                </div>
                                            </div>
                                        </div>
                                    ))
                                }
                            </div>
                        </div>
                    ))
                }
            </div>
        </div>
    );
}


export default Facilities;
