export const CHANGE = 'CHANGE';
export const ADD_ROUTE = 'ADD_ROUTE';

const ERROR = 'ERROR';
export const REMOVE_ERROR = 'REMOVE_ERROR';


export const initialState = [{
    from: '',
    to: '',
    departure: null,
    adults: 1,
    childrens: 0,
    infants: 0,
    error: {
        from: null,
        to: null,
        departure: null, 
        adults: null,
        childrens: null,
        infants: null,
    }
}, {
    from: '',
    to: '',
    departure: null,
    error: {
        from: null,
        to: null,
        departure: null, 
    }
}];



export function multicityReducer(state, action) {
    const {index, name, value} = action;
    switch(action.type){
        case CHANGE:            
            let newState = [...state];
            if (name === 'adults' || name === 'childrens' ||name === 'infants') {
                newState[0][name] = value;
            } else {
                newState[action.index][action.name] = action.value;
                if (name === 'to' && index < state.length - 1) {
                    newState[index + 1]['from'] = value;
                }
            }
            return newState;
        case ERROR:
            let newState2 = [...state];
            newState2[index]['error'][name] = value;
            return newState2;
        case ADD_ROUTE:
            return [...state, {from: state[state.length - 1].to, to: '', departure: null, error: {
                from: null,
                to: null,
                departure: null, 
                adults: null,
                childrens: null,
                infants: null,
            }}];

        case REMOVE_ERROR:
            let removedState = [...state];
            removedState[index].error[name] = null
            return removedState;
        default: 
            return state;
    }
}