import React, { useReducer, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import SearchIcon from '@material-ui/icons/Search';
import { DatetimeInput, Button, AutoComplete } from '../../../../../components';
import { CHANGE, ERROR, REMOVE_ERROR, cabReducer, initialState } from './CabReducer';


const FROM_PLACES = ['MarthaHalli, Bengaluru, Karnataka', 'UttaraHalli, Bengaluru, Karnataka', 'Hebbal, Bengaluru, Karnataka', 'Malleshwaram, Bengaluru, Karnataka', 'Akshaya Nagara, Bengaluru, Karnataka'];

const TO_PLACES = ['BTM Layout, Bengaluru, Karnataka', 'HSR Layout, Bengaluru, Karnataka', 'M G Road, Bengaluru, Karnataka', 'Kengeri, Bengaluru, Karnataka', 'Devanhalli, Bengaluru, Karnataka',];


function HolidayForm(props){
    const [state, dispatch] = useReducer(cabReducer, initialState);
    const history = useHistory();
    const { error } = state;

    const validate = () => {
        let flag = true;
        if (!state.from) {
            dispatch({type: ERROR, name: 'from', value: 'Please enter a leaving'});
            flag = false;
        }
        if (!state.to) {
            dispatch({type: ERROR, name: 'to', value: 'Please enter a destination'});
            flag = false;
        }
        if (!state.departure) {
            dispatch({
                type: ERROR,
                name: 'departure',
                value: 'Please enter a departure date',
            });
        }
        if (!state.departure_time) {
            dispatch({
                type: ERROR,
                name: 'departure_time',
                value: 'Please enter a departure time',
            });
        }
        return flag;
    }

    const onSubmit = () => {
       if (validate()) {
        //    history.push(`/search/cab?from=${state.from}&to=${state.to}&departure=${state.departure}&departure_time=${state.departure_time}`);
        console.log(history);
       }
    }   

    useEffect(() => {
        if (state.from && error.from) {
            console.log('I am in...\n');
            dispatch({type: REMOVE_ERROR, name: 'from'});
        }
    }, [state.from, error.from]);

    useEffect(() => {
        if (state.to && error.to) {
            dispatch({type: REMOVE_ERROR, name: 'to'});
        }
    }, [state.to, error.to]);

    useEffect(() => {
        if (state.departure && error.departure) {
            dispatch({type: REMOVE_ERROR, name: 'departure'});
        }
    }, [state.departure, error.departure]);

    useEffect(() => {
        if (state.departure_time && error.departure_time) {
            dispatch({type: REMOVE_ERROR, name: 'departure_time'});
        }
    }, [state.departure_time, error.departure_time]);
    
    return(
        <div>
            <div>
                <Grid container spacing={2}>
                    <Grid item md={3} sm={6} xs={12}>
                        <AutoComplete 
                            name='from'
                            value={state.from}
                            label='Leaving From'
                            onChange={(event, newValue) => dispatch({type: CHANGE, name: 'from', value: newValue})}
                            error={error.from}
                            options={FROM_PLACES} 
                        />                    
                    </Grid>
                    <Grid item md={3} sm={6} xs={12}>
                        <AutoComplete 
                            name='to'
                            value={state.to}
                            label='Destination'
                            onChange={(event, newValue) => dispatch({type: CHANGE, name: 'to', value: newValue})}
                            error={error.to}
                            options={TO_PLACES}
                        />                    
                    </Grid>
                    <Grid item md={3} sm={6} xs={12}>
                        <DatetimeInput 
                            label='Departure'
                            name='departure'
                            value={state.departure}
                            onChange={(date) =>{console.log(date); dispatch({type: CHANGE, name: 'departure', value: date})}}
                            error={error.departure}
                            minutesStep={5}
                        />
                    </Grid>
                    <Grid item md={3} sm={6} xs={12}>
                        <DatetimeInput 
                            label='Return'
                            name='return'
                            value={state.return}
                            minDate={state.departure || new Date()}
                            onChange={(date) =>{console.log(date); dispatch({type: CHANGE, name: 'return', value: date})}}
                            error={error.departure}
                            minutesStep={5}
                        />
                    </Grid>
                </Grid>    
                <div className='m-t-12'>
                    <Grid container justify='flex-end'>
                        <Button 
                            icon={<SearchIcon />}
                            text='Search' 
                            onClick={onSubmit} 
                        />    
                    </Grid>    
                </div>               
            </div>
        </div>
    );
}


export default HolidayForm;