import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Chip from '../../../../components/chip';
import Text from '../../../../components/text';


const useStyles = makeStyles((theme) => ({
    box: {
        marginTop: theme.spacing(2),
        padding: theme.spacing(1.5),
        borderRadius: 2,
        backgroundColor: 'rgba(255, 127, 80, 0.16)',
    }
}));


function ReviewOverview(props){
    const classes = useStyles();
    return(
        <div className={classes.box}>
            <div className='r-f a-c'>
                <div>
                    <div className='r-f j-c'>
                        <Chip label='4.2/5' size='small' color='success' />
                    </div>
                    <div className='m-t-8'>
                        <Text text='1020 Reviews' variant='meta' textAlign='center' />
                    </div>
                </div>                
                <div className='f-g-1' />
                <div className='m-l-6'>
                    <Text text='Very Good' variant='title' />
                </div>               
            </div>
            <div className=''>
                <div className='r-f-w a-c'>
                    <div className='m-r-12 m-t-12'>
                        <Chip label='Clean Room' size='small' />
                    </div>
                    <div className='m-r-12 m-t-12'>
                        <Chip label='Good Room' size='small' />
                    </div>
                    <div className='m-r-12 m-t-12'>
                        <Chip label='Good Property' size='small' />
                    </div>
                </div>
            </div>
            <div className='m-t-12'>
                <Text variant='meta' text='View all Reviews' />
            </div>
        </div>
    );
}


export default ReviewOverview;