import React, { useEffect, useReducer } from 'react';
import { useHistory } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import SearchIcon from '@material-ui/icons/Search';
import { CHANGE, ERROR, REMOVE_ERROR, initialState, roundTriptReducer } from './RoundTripReducer';
import { DateInput, Button, AutoComplete } from '../../../../../../components';
import PassengerDetails from '../form/PassengerDetails';


function RoundTrip(props){
    const [state, dispatch] = useReducer(roundTriptReducer, initialState);
    const { error } = state;    
    const history = useHistory();

    const validate = () => {
        let flag = true;
        if (!state.from) {
            dispatch({type: ERROR, name: 'from', value: 'Please enter a leaving place'});
            flag = false;
        }
        if (!state.to) {
            dispatch({
                type: ERROR,
                name: 'to',
                value: 'Please enter a destination',
            });
            if (flag) {
                flag = false;
            }
        }
        if (!state.departure) {
            dispatch({
                type: ERROR,
                name: 'departure',
                value: 'Please enter a departure date',
            });
            if (flag) {
                flag = false;
            }
        }
        if (!state.return) {
            dispatch({
                type: ERROR,
                name: 'return',
                value: 'Please enter a return date',
            });
            if (flag) {
                flag = false;
            }
        }
        return flag;
    }

    const onSubmit = () => {
        console.log('Vaidating..\n');
        if (validate()) {
            console.log(history);
            // history.push(`/search/flights?trip=round&leaving_from=${state.from}&going_to=${state.to}&departure=${state.departure}&return=${state.return}&adults=${state.adults}&childrens=${state.childrens}&infants=${state.infants}`)
        }
    }

    useEffect(() => {
        if(state.from && error.from) {
            console.log('Removing...\n');
            dispatch({type: REMOVE_ERROR, name: 'from'});
        }
    }, [state.from, error.from])

    useEffect(() => {
        if (state.to && error.to) {
            dispatch({type: REMOVE_ERROR, name: 'to'});
        }
    }, [state.to, error.to]);

    useEffect(() => {
        if (state.departure && error.departure) {
            dispatch({type: REMOVE_ERROR, name: 'departure'});
        }
    }, [state.departure, error.departure]);
    return (
        <div className=''>
            <div className=''>
                <div className='r-f-w a-c'>
                    <div className='round-trip-item'>
                        <AutoComplete 
                            name='from'
                            value={state.from}
                            label='Leaving From'
                            onChange={(event, value) => dispatch({type: CHANGE, name: 'from', value: value})}
                            error={error.from}
                        />      
                    </div>
                    <div className='round-trip-item'>
                        <AutoComplete
                            name='to'
                            value={state.to}
                            label='Going to'
                            onChange={(event, value) => dispatch({type: CHANGE, name: 'to', value: value})}
                            error={error.to}
                        />
                    </div>
                    <div className='round-trip-item'>
                        <DateInput 
                            label='Departure'
                            name='departure'
                            value={state.departure}
                            onChange={(date) => dispatch({type: CHANGE, name: 'departure', value: date})}
                            error={error.departure}
                        />
                    </div>
                    <div className='round-trip-item'>
                        <DateInput 
                            label='Return'
                            name='return'
                            value={state.return}
                            minDate={state.departure}
                            onChange={(date) => dispatch({type: CHANGE, name: 'return', value: date})}
                            error={error.return}
                        />
                    </div>
                    <div className='round-trip-item'>
                        <PassengerDetails state={state} dispatch={dispatch} />
                    </div>
                </div>
                <div className='m-t-12'>
                    <Grid container justify='flex-end'>
                        <Button icon={<SearchIcon />} text='Search' onClick={onSubmit} />
                    </Grid>                    
                </div>
            </div>
        </div>
    );
}

export default RoundTrip;
