import React from 'react';
import { makeStyles } from '@material-ui/core';
import Button from '../../../../components/button';
import Chip from '../../../../components/chip';
import Text from '../../../../components/text';
import animatedScrollTo from 'animated-scroll-to';


const useStyles = makeStyles((theme) => ({
    card: {
        borderRadius: 2,
        padding: theme.spacing(1.5),
        backgroundColor: 'rgba(2, 182, 240, 0.16)',
    }
}));


function Prices(props){
    const classes = useStyles();

    const onClick = () => {
        const { roomRef } = props;
        if(roomRef && roomRef.current) {
            animatedScrollTo(roomRef.current.offsetTop - 200);
        }
    }
    return (
        <div className={classes.card}>
            <div>
                {/* <div className='r-f a-c j-c'>
                    <Chip label='4.5/5' size='small' color='success' />
                    <div className='m-l-6'>
                        <Text variant='meta' text='1250 Reviews' />
                    </div>
                </div> */}
                <div className='m-t-12'>
                    <div className='r-f a-c j-c'>
                        <Text variant='meta' text='Save 50$' />
                        <div className='m-l-6'>
                            <Chip label='10% offer' color='primary' size='small' />
                        </div>
                    </div>
                </div>
                <div className='m-t-12'>
                    <div className='r-f a-c j-c'>
                        <Text color='primary' variant='heading' text='$512'/>
                        <div className='m-l-6'>
                            <Text variant='meta' text='per night' />
                        </div>                        
                    </div>
                </div>
                <div className='m-t-12'>
                    <div className='r-f a-c j-c'>
                        <Button onClick={onClick} text='Reserve now' />
                    </div>
                </div>
            </div>
        </div>
    );
}


export default Prices;