import React, { useEffect, useReducer, useState } from 'react';
import { Redirect } from 'react-router-dom';
import Button from '../../../components/button';
import TextInput from '../../../components/input/textInput';
import { CHANGE, ERROR, REMOVE_ERROR, signinReducer, signupState } from '../signinForm/SigninReducer';


function SignupForm(props){
    const [state, dispatch] = useReducer(signinReducer, signupState);
    const [isRedirect, setRedirect] = useState(false);

    const validate = () => {
        let flag = false;
        if (state.email === 'johndoe@gmail.com' && state.password === 'johndoe@96'){
            flag = true;
        } else {
            dispatch({type: ERROR, name: 'email', value: 'This is email is not registered with us'});
            dispatch({type: ERROR, name: 'password', value: 'Password do not match'});
        }
        return flag;
    } 

    const onSubmit = () => {
        if (validate()) {
            setRedirect(true);
        }   
    }

    useEffect(() => {
        if (state.email && state.error.email) {
            dispatch({type: REMOVE_ERROR, name: 'email'});
        }
    }, [state.email, state.error.email]);

    useEffect(() => {
        if (state.password && state.error.password) {
            dispatch({type: REMOVE_ERROR, name: 'password'});
        }
    }, [state.password, state.error.password])

    if (isRedirect) {
        return (
            <Redirect to='/' />
        )
    }
    return (
        <div>
            <div className='p-tb-12'>
                <TextInput 
                    label='Email'
                    name='email'
                    onChange={(event) => dispatch({type: CHANGE, name: 'email', value: event.target.value})}
                    value={state.email}
                    error={state.error.email}
                />
            </div>
            <div className='p-tb-12'>
                <TextInput 
                    label='Name'
                    name='name'
                    onChange={(event) => dispatch({type: CHANGE, name: 'name', value: event.target.value})}
                    value={state.name}
                    error={state.error.name}
                />
            </div>
            <div className='p-tb-12'>
                <TextInput 
                    label='Password'
                    name='password'
                    onChange={(event) => dispatch({type: CHANGE, name: 'password', value: event.target.value})}
                    value={state.password}
                    error={state.error.password}
                />
            </div>
            <div className='p-tb-12'>
                <TextInput 
                    label='Confirm Password'
                    name='confirm_password'
                    onChange={(event) => dispatch({type: CHANGE, name: 'confirm_password', value: event.target.value})}
                    value={state.confirm_password}
                    error={state.error.confirm_password}
                />
            </div>
            <div className='p-tb-12'>
                <div className='r-f j-e'>
                    <Button 
                        text='Create an account'
                        onClick={onSubmit}
                    />
                </div>
            </div>
        </div>
    );
}


export default SignupForm;
