// It's better to see something once than hear about it a hundred times
import React from 'react';
import { Route, Switch, Redirect } from 'react-router-dom';
import { 
    Admin, 
    Flight,
    Holiday,
    Home, 
    Hotel, 
    HotelList, 
    HotelForm,
    Invest,
    Managment,
    Product,
    SearchFlights,
    SearchHolidays,
    SearchHotels, 
    ShopHome,
    Signin
} from '../pages';


// Router component where all the routes are defined
function CwbRouter(props){
    return (
        <Switch>
            <Route exact path = '/' component={Home} />
            <Route exact path = '/admin' render={() => <Redirect to='/admin/dashboard'/> } />
            <Route exact path = '/admin/hotel/create' component={HotelForm} />
            <Route path='/admin' component={Admin} />
            <Route exact path = '/flight/:slug/' component={Flight} />
            <Route exact path='/holiday/:slug/' component={Holiday} />
            <Route exact path='/hotel/list/' component={HotelList} />
            <Route exact path = '/hotel/:slug/' component={Hotel} />   
            <Route exact path = '/invest/' component={Invest} />
            <Route exact path = '/managment/' component={Managment} />
            <Route exact path = '/product/:slug/' component={Product} />
            <Route exact path = '/search/flights/' component={SearchFlights} />
            <Route exact path = '/search/holidays' component={SearchHolidays} />  
            <Route exact path = '/search/hotels' component={SearchHotels} /> 
            <Route exact path = '/shop/' component={ShopHome} />  
            <Route exact path = '/signin/' component={Signin} />
            <Route exact path = '/signup/' component={Signin} />
        </Switch>
    );
}


export default CwbRouter;