import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Chip from '../../../../components/chip';
import Text from '../../../../components/text';


const useStyles = makeStyles((theme) => ({
    card: {
        // padding: theme.spacing(1),
        borderRadius: 2,
        boxShadow: theme.shadows[2],
        [theme.breakpoints.up('md')]: {
            '&:hover': {
                boxShadow: theme.shadows[4],
            }
        },
        '&:hover .p-c-photo': {
            transform: 'scale(1.2)',
        }
    },
    photo: {
        width: '100%',
        height: 'auto',
        overflow: 'hidden',
    },
    content: {
        padding: theme.spacing(1),
    }
}));


function Card(props){
    const classes = useStyles();
    const { product } = props;
    return (
        <div className={classes.card}>
            <div className={`${classes.photo}`}>
                <img src={product.photo} alt='' className='p-c-photo' />
            </div>
            <div className={classes.content}>
                <Text text={product.title} variant='sideheading' />
                <div className='r-f a-c m-t-6'>
                    <Chip color='success' label={`${product.rating}/5`} size='small' />
                    <div className='m-l-6'>
                        <Text variant='meta' text='102 Ratings & 14 Reviews' />     
                    </div> 
                </div>
                <div className='r-f a-c m-t-6'> 
                    <Text text={`$${product.new_price}`} variant='heading' color='primary' />
                    <div className='m-l-12'>
                        <Text variant='meta' text={`$${product.price}`} strike={true} />
                    </div>
                    <div className='m-l-12'>
                        <Text text={`${product.offer}% offer`} color='secondary' variant='title' />
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Card;
