import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Button from '../../../../../../components/button';
import Text from '../../../../../../components/text';


const useStyles = makeStyles((theme) => ({
    container: {
        padding: 12,
        border: `1px solid #cecece`,
        borderRadius: 2,
    },
    photo: {
        width: 240,
        height: 120,
    }
}));


function Cab(props){
    const classes = useStyles();
    return (
        <div className={classes.container}>
            <div className='r-f'>
                <div className='m-r-12'>
                    <div className={classes.photo}>
                        <img src={'https://imgak.mmtcdn.com/holidays/images/dynamicDetails/private_transfer.png'} alt='' />
                    </div>
                </div>
                <div className='f-g-1'>
                    <Text text={'Airport to hotel in Dubai'} variant='meta' />
                    <Text text='Private Transfer' variant='title' />
                    <div className='m-t-8'>
                        <Text text={'Duration'} variant='meta' />
                        <Text text='1 Hrs' variant='text' />
                    </div>
                    <div className='r-f j-e m-t-8'>
                        <div className='f-g-1' />
                        <div className='m-l-12'>
                            <Button 
                                text='Change' 
                                variant='outlined' 
                                color='default'
                                size='small' 
                            />
                        </div>                
                        <div className='m-l-12'>
                            <Button 
                                text='Remove' 
                                variant='outlined' 
                                color='default'
                                size='small' 
                            />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Cab;
