import React from 'react';
import { Link } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';
import CheckRouned from '@material-ui/icons/CheckRounded';
import Chip from '../chip';
// import FavouriteButton from '../favouriteButton';
import Text from '../text';
import Includes from './includes';


const useStyles = makeStyles((theme) => ({
    container: {
        marginBottom: theme.spacing(1.5),
        border: `1px solid ${theme.palette.type === 'dark' ? 'transparent': '#cecece'}`,
        backgroundColor: theme.palette.type === 'dark' ? '#424242' : '#fff', 
        borderRadius: 2,
        '&:hover': {
            borderColor: theme.palette.primary.main,
        }
    },
    content: {
        padding: 12,
        width: 'calc(100% - 168px)',
    },
    photo: {
        marginRight: 12,
        height: 200,
        width: 260,
        minWidth: 260,
    },
    textContainer: {
        width: 'calc(100% - 272px)',
    },
    facility: {
        marginRight: theme.spacing(1.5),
        marginBottom: theme.spacing(1.5),
    },
    icon: {
        marginRight: 6,
        height: 20,
        width: 20,
        color: theme.palette.type === 'dark' ? '#f1f1f1' : '#4a4a4a',
    },
    priceContainer: {
        padding: '12px 24px',
        borderRadius: 2,
        backgroundColor: 'rgba(2, 182, 240, 0.16)',
        width: 168,
        minWidth: 168,
        position: 'relative'
    },
    // favouriteButton: {
    //     position: 'absolute',
    //     top: 12,
    //     right: 12,
    // }
}))

function Item(props){
    const classes = useStyles();
    const { item } = props;
    let meta = null;
    if (item.type === 'tour') {
        meta = `${item.nights}N ${item.nights + 1}D`;
        if (item.stays && item.stays.length) {
            item.stays.map((stay, index) => {
                meta = `${meta}${index === 0 ? ' - ' : ' . '}${stay}`;
                return stay;
            })
        }
    }
    const { type } = item;
    return(   
        <Link to={`/${item.type === 'hotel' ? 'hotel' : 'holiday'}/1`} className={classes.link}>
            <div className={classes.container}>
                <div className='r-f'>
                    <div className={classes.content}>
                        <div className='r-f'>
                            <div className={classes.photo}>
                                <img src={item.photo} alt={item.title} />
                            </div>
                            <div className={classes.textContainer}>
                                <Text variant='sideheading' text={item.title} />
                                <Text variant='meta' text={type === 'tour' ? meta : item.location} />
                                {
                                    type === 'hotel' ?
                                        <div className='m-t-12'>
                                            <div className='r-f a-c'>
                                                <Chip label='Villa' size='small' />
                                            </div>                                            
                                        </div>
                                    :
                                        null
                                }
                                {
                                    item.services ?
                                        <div className='m-t-12'>
                                            <div className='r-f-w a-c'>
                                                {
                                                    item.services.split(',').map((text, index) => (
                                                        <div className={classes.facility} key={index}>
                                                            <div className='r-f a-c'>
                                                                <div className={classes.icon}>
                                                                    <CheckRouned color='secondary' fontSize='small' />
                                                                </div>
                                                                <div>
                                                                    <Text variant='meta' text={text} />
                                                                </div>
                                                            </div>
                                                        </div>
                                                    ))
                                                }
                                            </div>                                    
                                        </div>
                                    :
                                        null
                                }
                                {
                                    type === 'tour' && item.suitable_for && item.suitable_for.length ?
                                        <div className='m-t-8'>
                                            <div className='r-f-w a-c'>
                                                {
                                                    item.suitable_for.map((suitable, index) => (
                                                        <div className={classes.facility} key={index}>
                                                            <Chip label={suitable} size='small' />
                                                        </div>
                                                    ))
                                                }
                                            </div>
                                        </div>
                                    :
                                        null
                                }
                                {
                                    type === 'tour' ?
                                        <Includes item={item} />
                                    :
                                        null
                                }
                                {
                                    type === 'tour' && item.activities && item.activities.length ?
                                        <div className='m-t-12'>
                                            <div className='r-f-w a-c'>
                                                {
                                                    item.activities.map((activity, index) => (
                                                        <div className={classes.facility} key={index}>
                                                            <div className='r-f a-c'>
                                                                <div className={classes.icon}>
                                                                    <CheckRouned color='secondary' fontSize='small' />
                                                                </div>
                                                                <div>
                                                                    <Text variant='meta' color='secondary' text={activity} />
                                                                </div>
                                                            </div>
                                                        </div>
                                                    ))
                                                }
                                            </div>
                                        </div>
                                    :
                                        null
                                }
                                {
                                    type === 'hotel' ?
                                        <div className=''>
                                            <div className='r-f a-c'>
                                                <div>
                                                    <Text variant='meta' text='More options:' />
                                                </div>
                                                <div className='m-l-6'>
                                                    <Text variant='title' color='primay' text='Breakfast included' />
                                                </div>
                                            </div>
                                        </div>
                                    :
                                        null
                                }
                            </div>
                        </div>                        
                    </div>
                    <div className={classes.priceContainer}>
                        <div className='c-f h-100 w-100'>
                            <div className=''>
                                <div className='r-f a-c j-c'>
                                    <Chip label='4.5/5' size='small' color='success' />
                                </div>
                                <div className='m-t-6'>
                                    <Text variant='meta' text='1250 Reviews' textAlign='center' />
                                </div>
                            </div>
                            <div className='f-g-1' />
                            <div className='w-100'>
                                <div className='r-f a-c j-c'>
                                    <Text text='Save $50' color='primary' variant='meta' />
                                    <div className='m-l-6'>
                                        <Chip label={`${10}%`} color='primary' size='small' />
                                    </div>
                                </div>
                            </div>
                            <div className='m-t-8'>
                                <div>
                                    <Text 
                                        color='primary'
                                        text={`${item.price}`} 
                                        textAlign='center' 
                                        variant='heading' 
                                    />
                                </div>
                                <div>
                                    <Text text='price per person' textAlign='center' variant='meta' />
                                </div>
                                <div className='m-t-6'>
                                    <div className='r-f a-c j-c'>
                                        <Text variant='meta' text='Starts from' />
                                        <div className='m-l-6'>
                                            <Text color='primary' variant='title' text='$200' />
                                        </div>
                                    </div>                                   
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </Link>    
    );
}


export default Item;