export const initialState = {
    from: '',
    to: '',
    departure: null,
    return: null,
    departure_time: null,
    return_time: null,
    error: {
        from: null,
        to: null,
        departure: null,
        return: null,
        departure_time: null,
        return_time: null,
    }
}


export const CHANGE = 'CHANGE';
export const ERROR = 'ERROR';
export const REMOVE_ERROR = 'REMOVE_ERROR';


export function cabReducer(state, action){
    switch (action.type) {
        case CHANGE: 
            return {...state, [action.name]: action.value};
        case ERROR:
            return {...state, error: {...state.error, [action.name]: action.value}};
        case REMOVE_ERROR:
            return {...state, error: {...state.error, [action.name]: null}};
        default:
            return state;
    }
}