import React from 'react';
import { Tab, Tabs } from '@material-ui/core';
import BeachAccessRoundedIcon from '@material-ui/icons/BeachAccessRounded';
import HotelRoundedIcon from '@material-ui/icons/HotelRounded';
import FlightRoundedIcon from '@material-ui/icons/FlightRounded';
import LocalTaxiRoundedIcon from '@material-ui/icons/LocalTaxiRounded';
// import DeckRoundedIcon from '@material-ui/icons/DeckRounded';


function a11yProps(index) {
    return {
        id: `simple-tab-${index}`,
        'aria-controls': `simple-tabpanel-${index}`,
    };
}

// const MENUS = ['Flights', 'Hotels', 'Tours', 'Holidays', 'Cabs', 'Trains'];


const MENU = [
    {
        text: 'Holidays',
        icon: <BeachAccessRoundedIcon fontSize='small' />
    },
    {
        text: 'Hotels',
        icon: <HotelRoundedIcon fontSize='small' />
    },
    {
        text: 'Private Flights',
        icon: <FlightRoundedIcon fontSize='small' />
    },
    {
        text: 'Cabs',
        icon: <LocalTaxiRoundedIcon fontSize='small' />
    },
    // {
    //     text: 'Tours',
    //     icon: <DeckRoundedIcon fontSize='small' />
    // },  
];


function Tabbar(props){
    const { tab, onChange } = props;
    return (
        <div style={{maxWidth: '100%'}}>
            <Tabs 
                value={tab} 
                onChange={onChange} 
                aria-label="simple tabs example"
                indicatorColor="primary"
                textColor="primary"
                variant="scrollable"
                scrollButtons="auto"
            >
                {
                    MENU.map((menu, index) => (
                        <Tab 
                            label={menu.text }
                            icon={menu.icon}
                            key={index}
                            {...a11yProps(index)}
                        />
                    ))
                }
            </Tabs>
        </div>
    );
}


export default Tabbar;