import React, { useEffect, useState } from 'react';
import Tab from '@material-ui/core/Tab';
import Tabs from '@material-ui/core/Tabs';
import animatedScrollTo from 'animated-scroll-to';


function Tabbar(props){
    const [tab, setTab] = useState(0);
    const onChange = (event, value) => {
        setTab(value);
        if (value === 0) {
            animatedScrollTo(0);
        } else {
            const ref = props.references[value];
            if (ref && ref.current) {
                animatedScrollTo(ref.current.offsetTop - 120);
            }
        }     
    }

    useEffect(() => {
        const onScroll = () => {
            const scrollTop = document.documentElement.scrollTop;
            let newTab;
            for(let i = 0; i < props.references.length; i++) {
                const current = props.references[i].current;
                if (current) {
                    if (i === 0 && (scrollTop <= (props.references[1].current.offsetTop - (window.innerHeight / 2)))){
                        newTab = 0;
                        break;
                    } else if (i === props.references.length - 1 && (scrollTop > (current.offsetTop - (window.innerHeight / 2)))) {
                        newTab = i;
                        break;
                    } else if ((scrollTop > (current.offsetTop - (window.innerHeight)) && (scrollTop <= (current.offsetTop + current.offsetHeight - (window.innerHeight / 2))))){
                        newTab = i;
                    }
                } 
            }
            if (newTab >= 0) {
                setTab(newTab);
            }      
        };
        window.addEventListener('scroll', onScroll);
        return () => {
            window.removeEventListener('scroll', onScroll);
        }
    }, [props.references]);
    return (
        <Tabs
            value={tab}
            onChange={onChange}
            indicatorColor="primary"
            textColor="primary"
            variant="scrollable"
            scrollButtons="auto"
        >
            {
                props.tabs ?
                    props.tabs.map((tab, index) => (
                        <Tab label={tab} key={index} />
                    ))
                :
                    <React.Fragment>
                        <Tab label='Overview' />
                        <Tab label='Flight Details' />
                        <Tab label='Cost Details' />
                        <Tab label='Specification' />
                        <Tab label='Policies' />
                        <Tab label='Reviews' />
                    </React.Fragment>
            }

        </Tabs>
    );
}

export default Tabbar;
