import React, { useState } from 'react';
import IconButton from '@material-ui/core/IconButton';
import { makeStyles } from '@material-ui/core/styles';
import FavouriteBorderRounded from '@material-ui/icons/FavoriteBorderRounded';
import FavouriteRounded from '@material-ui/icons/FavoriteRounded';


const useStyles = makeStyles((theme) => ({
    normal: {
        color: 'rgb(62, 62, 62)',
    },
    white: {
        color: '#fff',
    },
    theme: {
        color: theme.palette.primary.main,
    }
}))


function FavouriteButton(props){
    const [isFavourite, setFavourite] = useState(false);
    const classes = useStyles();
    return(
        <IconButton 
            className={isFavourite ? classes.theme : props.color === 'white' ? classes.white : classes.normal} onClick={() => setFavourite(!isFavourite)}
        >
            {
                isFavourite ?
                    <FavouriteRounded fontSize='small' />
                :
                    <FavouriteBorderRounded fontSize='small' />
            }
        </IconButton>
    );
}


export default FavouriteButton;