import React, { useRef } from 'react';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import Skeleton from '@material-ui/lab/Skeleton';
import { Text, FavouriteButton } from '../../../components';
import Header from '../../holiday/header';
import Tabbar from '../../flight/info/overview/tabbar';
import About from './about';
import Photos from './photos';
import Rooms from './rooms';
import Prices from './prices';
import RoomImages from './roomImages';
import ReviewOverview from './reviewOverview';
import MapOverview from './mapOverview';
import Facilities from './facilities';
import Location from './location';
import Policies from './policies';
import Review from './review';


const useStyles = makeStyles((theme) => ({
    header: {
        marginBottom: theme.spacing(1),
    }
}));


function Info(props){
    const classes = useStyles();
    const overViewRef = useRef();
    const roomsRef = useRef();
    const facilitiesRef = useRef();
    const locationRef = useRef();
    const policyRef = useRef();
    const reviewRef = useRef(); 
    const { hotel, isLoading } = props; 
    return (
        <div>
            <Header 
                title={!isLoading && typeof hotel === 'object' ? hotel.title : ''}
                info='Hotel'
                isLoading={isLoading}
                tabbar={
                    <Tabbar 
                        references={
                            [overViewRef, roomsRef, facilitiesRef, locationRef, policyRef, reviewRef]
                        }
                        tabs={['Overview', 'Rooms', 'Facilities', 'Location', 'Policy', 'Review']}
                    />    
                }
            />
            <div className={classes.header} ref={overViewRef}>
                <div className='r-f a-c'>
                    <div>
                        <Text text='Royal Orchid Central' />
                        <Text 
                            text='47/1 Dickenson Road,Manipal Centre, Bengaluru - 560042' 
                            variant='meta' 
                        />
                    </div>
                    <div className='f-g-1' />
                    <FavouriteButton />
                </div>                
            </div>     
            <Grid container spacing={2}>
                <Grid item xs={12} sm={12} md={7} >
                    <Photos isLoading={isLoading} hotel={hotel} />
                </Grid>
                <Grid item xs={12} sm={12} md={5}>
                    <div className=''>
                        <div>
                            {
                                isLoading ?
                                    <div>
                                        <Skeleton style={{height: 24, width: '60%'}} variant='text' />
                                        <Skeleton style={{height: 16, width: '40%'}} variant='text' />
                                    </div>
                                :
                                    <div>
                                        <Text text={hotel.title} />
                                        <Text 
                                            text={`${hotel.city}, ${hotel.state}, ${hotel.county}`} 
                                            variant='meta' 
                                        />
                                    </div>
                            }
                            
                        </div>
                    </div>   
                    <div className='m-t-12'>
                        <Grid container spacing={2}>
                            <Grid item xs={12} sm={6}>
                                <RoomImages />
                                <MapOverview />
                            </Grid>
                            <Grid item xs={12} sm={6}>
                                <Prices roomRef={roomsRef} />
                                <ReviewOverview reviewRef={reviewRef} />
                            </Grid>
                        </Grid>
                    </div>                   
                </Grid>
            </Grid>
            <div className='m-t-12'>
                <About isLoading={isLoading} hotel={hotel} />
            </div>
            <div ref={roomsRef}>
                <Rooms />
            </div>
            <div ref={facilitiesRef}>
                <Facilities />
            </div>
            <div ref={locationRef}>
                <Location />
            </div>
            <div ref={policyRef}>
                <Policies />
            </div>
            <div ref={reviewRef}>
                <Review />
            </div>
            <div className='m-tb-12' />
        </div>
    );
}


export default Info;
