import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Chip from '../../../../components/chip';
import Text from '../../../../components/text';


const useStyles = makeStyles((theme) => ({
    item: {
        marginBottom: theme.spacing(1.5),
        border: '1px solid #cecece',
        borderRadius: 2,
        padding: theme.spacing(1.5),
    }
}));


function ReviewItem(props){
    const classes = useStyles();
    const { review } = props;
    return (
        <div className={classes.item}>
            <div className='r-f a-c'>
                <div>
                    <Text variant='title' text={review.title} />
                    <Text variant='meta' text={`${review.user} - ${review.timestamp}`} />
                    <div className='m-t-8'>
                        <Text variant='text' text={review.text} />
                    </div>
                </div>
                <div className='f-g-1' />
                <div className='m-l-12' />
                <Chip label={review.rating} color={review.rating > 3 ? 'success' : 'danger'} size='small' />
            </div>
        </div>
    );
}

export default ReviewItem;
