import React from 'react';
import Skeleton from '@material-ui/lab/Skeleton';
import Text from '../../../../components/text';


// const TEXT = "This luxury riverfront resort is known for its award-wining vegetarian restaurant Jalsa, and Zaara spa with Mughal inspired decor.\n\n2.6 km from Baga Beach, 3.5 km from Calangute Beach, 600 m from Arpora Market, Poolside multi-cuisine restaurant \n\n Resort Rio in Arpora is a five star deluxe spa property that offers visitors to Goa luxurious accommodation, a host of world-class facilities and services, extensive spa treatments, significant conference/banquet facilities, as well as warm hospitality. \n \n Resort Rio consists of a cluster of eateries serving international and domestic cuisines along with Goan and Konkani delicacies. The hotel also has a well-stocked bar with pleasant ambience that offers a wide selection of beverages and appetizers.\n \n An epitome of recreational activities and an embodiment such as full-fledged spa called \"Spa Rio\" with a massage centre that offers Ayurvedic and Western treatments. \n \n In addition to various recreational activities it has well equipped business centre, babysitting service, internet facility, facilities for the physically challenged, doctor on call, laundry services and beach shuttle service. \n \n All its air-conditioned rooms are compact with latest gadgets like data port, DVD player on request, direct dialling telephone, Wi-Fi internet access and electronic safe. 24 hour room service is also available. \n Resort Rio is located at Arpora of Bardez area in North Goa and enjoys a convenient distance from Dabolim Airport and Thivim Railway Station."


function About(props){
    const { isLoading, hotel } = props;
    return (
        <div className='m-t-18'>
            <div>
                {
                    isLoading ?
                        <Skeleton style={{height: 24, width: '60%'}} variant='text' />
                    :
                        <Text text='About hotel' variant='sideheading' />
                }
                
            </div>
            {
                isLoading ? 
                    <div>
                        <div className='m-tb-6'>
                            <Skeleton style={{height: 16, width: '40%'}} variant='text' />
                        </div>
                        <div className='m-tb-6'>
                            <Skeleton style={{height: 16, width: '60%'}} variant='text' />
                        </div>
                        <div className='m-tb-6'>
                            <Skeleton style={{height: 16, width: '50%'}} variant='text' />
                        </div>
                        <div className='m-tb-6'>
                            <Skeleton style={{height: 16, width: '40%'}} variant='text' />
                        </div>
                        <div className='m-tb-6'>
                            <Skeleton style={{height: 16, width: '30%'}} variant='text' />
                        </div>
                        <div className='m-tb-6'>
                            <Skeleton style={{height: 16, width: '40%'}} variant='text' />
                        </div>
                        <div className='m-tb-6'>
                            <Skeleton style={{height: 16, width: '70%'}} variant='text' />
                        </div>
                    </div>
                :
                    <div>
                        <div className='m-t-8'>
                            <div className='r-f a-c'>
                                <Text variant='meta' text={`Check in ${hotel.check_in}`} />
                                <div className='m-lr-12'>
                                    <Text variant='meta' text='-' />
                                </div>
                                <Text variant='meta' text={`Check out ${hotel.check_out}`} />
                            </div>
                        </div>
                        <div className='m-t-12' style={{whiteSpace: 'pre-line'}}>
                            <div>
                                <Text variant='meta' text={hotel.description} />
                            </div>
                        </div>
                    </div>
            }
            
        </div>
    );
}

export default About;
