import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Text from '../../../../components/text';


const POLICIES = {
    'Guest Profile': ['Suitable for children', 'Bachelors allowed', 'Unmarried couples allowed', 'Accompanying Parents only'],
    'Safety and Hygiene': ['Social distancing measures are present in restaurant(s)', 'Utensils, crockery and supplies are sanitized before entering the kitchen', 'Masks and hair nets are mandatory for staff in restaurants', 'Quarantine protocols are being followed as per local government authorities', 'All rooms are disinfected using bleach or other disinfectant', 'Guests with fever are not allowed', 'Only those guests with safe status on Aarogya Setu app are allowed', 'Guests coming from containment zones are not allowed'],
    'Payment Related': ['Refundable security deposit before check-in is mandatory', 'Credit/debit cards are accepted'],
    'Food Arrangement': ['Outside food is not allowed in property premises'],
    'ID Proof Related': ['Passport, Aadhar, Driving License and Govt. ID are accepted as ID proof(s)', 'PAN Card is not accepted as ID proof(s)']
};


const useStyles = makeStyles((theme) => ({
    container: {
        marginTop: 64,
        marginBottom: theme.spacing(1.5),
        paddingBottom: theme.spacing(1.5),
        // borderBottom: `1px solid #cecece`,
    },
    ul: {
        margin: `12px 0`,
        padding: `0 0 0 24px`,
    }, 
    li: {
        margin: '0 6px',
        color: '#4a4a4a',
        fontSize: theme.typography.fontSize - 1,
    }
}));


function Policies(props){
    const classes = useStyles();
    return (
        <div className={classes.container}>
            <div className=''>
                <div>
                    <Text text='Policies' variant='sideheading' />
                </div>
                <div className='m-t-12'>
                    {
                        Object.keys(POLICIES).map((key,index) => (
                            <div key={index}>
                                <div>
                                    <Text variant='text' text={key} />
                                    <ul className={classes.ul}>
                                        {
                                            POLICIES[key].map((text, index) => (
                                                <li key={index}>
                                                    <Text variant='meta' text={text} />
                                                </li>
                                            ))
                                        }
                                    </ul>
                                </div>
                            </div>
                        ))
                    }
                </div>            
            </div>
        </div>
    );
}

export default Policies;
