import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Button from '../../../../../components/button';
import Chip from '../../../../../components/chip';
import Text from '../../../../../components/text';
import HolidayForm from './HolidayForm';


const useStyles = makeStyles((theme) => ({
    container: {
        marginTop: theme.spacing(2),
        padding: theme.spacing(1.5),
        border: `1px solid #cecece`,
        borderRadius: 2,
    },
    prices: {
        marginTop: theme.spacing(1.5),
        padding: theme.spacing(1.5),
        backgroundColor: `rgba(2, 182, 240, 0.16)`,
        borderRadius: 2,
    },
    footer: {
        borderTop: `1px solid rgba(0, 0, 0, 0.08)`,
    }
}));


function Prices(props){
    const classes = useStyles();
    return(
        <div className={classes.container}>
            <div>
                <HolidayForm />
            </div>
            <div className={classes.prices}>
                <div className='r-f a-c'>
                    <div className=''>
                        <Text text='$1200' variant='meta' strike={true} />
                        <div className='r-f a-c'>
                            <Text text='$1000' variant='heading' color='primary' />
                            <div className='m-l-6'>
                                <Text text='per person' variant='meta' textAlign='center' />
                            </div>
                        </div> 
                        <Text variant='meta' text='(Taxes are not included)' />                   
                    </div>
                    <div className='f-g-1' />
                    <div className='m-l-12'>
                        <Chip label='35% OFF' color='success' size='small' />
                    </div>                
                </div>
                <div className='m-t-12'>
                    <div className='r-f j-e'>
                        <Button text='Book Now' />
                    </div>
                </div>          
            </div>              
        </div>
    );
}


export default Prices;