import React from 'react';
import DashboardRoundedIcon from '@material-ui/icons/DashboardRounded';
import BarChartRoundedIcon from '@material-ui/icons/BarChartRounded';
import HotelRoundedIcon from '@material-ui/icons/HotelRounded';
import FlightRoundedIcon from '@material-ui/icons/FlightRounded';
import GroupRoundedIcon from '@material-ui/icons/GroupRounded';
import BeachAccessRoundedIcon from '@material-ui/icons/BeachAccessRounded';
import DeckRoundedIcon from '@material-ui/icons/DeckRounded';
import LocalTaxiRoundedIcon from '@material-ui/icons/LocalTaxiRounded';
import ConfirmationNumberRoundedIcon from '@material-ui/icons/ConfirmationNumberRounded';
import MenuItem from './MenuItem';


const MENUS = [
    {
        text: 'Dashboard',
        icon: <DashboardRoundedIcon fontSize='small' color='inherit'  />,
        link: '/admin/dashboard',
    },
    {
        text: 'Stats',
        icon: <BarChartRoundedIcon fontSize='small' color='inherit'  />,
        link: '/admin/stats',
    },
    {
        text: 'Bookings',
        icon: <ConfirmationNumberRoundedIcon fontSize='small' color='inherit'  />,
        link: '/admin/bookings',
    },
    {
        text: 'Users',
        icon: <GroupRoundedIcon fontSize='small' color='inherit'  />,
        link: '/admin/users',
    },
    {
        text: 'Flights',
        icon: <FlightRoundedIcon fontSize='small' color='inherit'  />,
        link: '/admin/flights',
    },
    {
        text: 'Hotels',
        icon: <HotelRoundedIcon fontSize='small' color='inherit'  />,
        link: '/admin/hotels',
    },
    {
        text: 'Tours',
        icon: <DeckRoundedIcon fontSize='small' color='inherit'  />,
        link: '/admin/tours',
    },
    {
        text: 'Holidays',
        icon: <BeachAccessRoundedIcon fontSize='small' color='inherit'  />,
        link: '/admin/holidays',
    },
    {
        text: 'Cabs',
        icon: <LocalTaxiRoundedIcon fontSize='small' color='inherit' />,
        link: '/admin/cabs',
    },
    {
        text: 'Tours',
        icon: <DeckRoundedIcon fontSize='small' color='inherit' />,
        link: '/admin/toursss',
    },
]


const Menu = () => {
    return (
        <div>
            <div>
                {
                    MENUS.map((menu, index) => (
                        <MenuItem menu={menu} key={index} />
                    ))
                }
            </div>
        </div>
    );
}

export default Menu;
