import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Flight from '@material-ui/icons/FlightRounded';
import Hotel from '@material-ui/icons/HotelRounded';
import Cab from '@material-ui/icons/LocalTaxiRounded';
import Star from '@material-ui/icons/StarRounded';
import Text from '../../text';


const useStyles = makeStyles((theme) => ({
    icon: {
        height: 20,
        width: 20,
        marginRight: 6,
    }
}));


function Includes(props){
    const classes = useStyles();
    return (
        <div className=''>
            <div className=''>
                <Text variant='meta' text='Included at this price' />
            </div>
            <div className='m-t-6'>
                <div className='r-f a-c'>
                    <div className='r-f a-c'>
                        <div className={classes.icon}>
                            <Flight fontSize='small' color='secondary' />
                        </div>
                        <div>
                            <Text variant='meta' text='Flight' />
                        </div>
                    </div>
                    <div className='r-f a-c m-l-12'>
                        <div className={classes.icon}>
                            <Hotel fontSize='small' color='secondary' />
                        </div>
                        <div>
                            <Text variant='meta' text='Hotel' />
                        </div>
                    </div>
                    <div className='r-f a-c m-l-12'>
                        <div className={classes.icon}>
                            <Cab fontSize='small' color='secondary' />
                        </div>
                        <div>
                            <Text variant='meta' text='Transfers' />
                        </div>
                    </div>
                    <div className='r-f a-c m-l-12'>
                        <div className={classes.icon}>
                            <Star fontSize='small' color='secondary' />
                        </div>
                        <div>
                            <Text variant='meta' text='Activities' />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Includes;
