import React from 'react';
import { Link } from 'react-router-dom';
import Button from '../../../button';
import Text from '../../../text';
import SwitchInput from '../../../input/switchInput';
import useTheme from '../../../../hooks/useTheme';

 
function Menu(props){
    const theme = useTheme();
    return (
        <div className='p-12'>
            <div className='p-lr-12'>
                <div className='r-f j-c'>
                    <Link to='/signin'>
                        <Button variant='outlined' text='Signin' style={{width: 180}} />
                    </Link>                   
                </div>
                <div className='m-t-6'>
                    <Text variant='meta' text='or' textAlign='center' />
                </div>
                <div className='m-t-6'>
                    <div className='r-f j-c'>
                        <Link to='/signup'>
                            <Button variant='outlined' color='secondary' text='Create an account' style={{width: 180}} />
                        </Link>                        
                    </div>
                </div>
                <div className='m-t-12'>
                    <div className='r-f a-c'>
                        <Text variant='text' text='Dark mode' />
                        <div className='f-g-1' />
                        <SwitchInput 
                            name='theme'
                            checked={theme.currentTheme === 'dark'}
                            onChange={(event) => theme.toggleTheme()}
                        />
                    </div>
                </div>
            </div>            
        </div>
    );
}


export default Menu;
