import React, { useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core';
import Bungalows from './photos/bungalows.jpg';
import Cottage from './photos/cottage.jpg';
import Empty from './photos/empty-lounges.jpg';
import Person from './photos/person.jpg';
import RedHam from './photos/red-ham.jpg';
import SandBars from './photos/sand-bars.jpg';
import QuoteBox from './quoteBox';

const PHOTOS = [Bungalows, Cottage, Empty, Person, RedHam, SandBars];


const useStyles = makeStyles((theme) => ({
    imageContainer: {
        width: '100%',
        height: '620px',
        display: 'flex',
        alignItems: 'center',
        overflow: 'hidden',
        [theme.breakpoints.down('md')]: {
            height: 'auto',
        },
        [theme.breakpoints.down('xs')]: {
            height: 'auto',
        }
    },
    image: {
        width:'100%',
        height: 'auto',
        minHeight: '700px',
        transition: 'all ease-in 350ms',
        [theme.breakpoints.down('md')]: {
            height: 'auto',
            width: '100%',
            minHeight: 'auto',
            minWidth: 'auto',
        }
    }
}));


function Gallery(props){
    const [index, setIndex] = useState(0);
    useEffect(() => {
        const interval = setInterval(() => {
            console.log('I am in...\n');
            setIndex(((index + 1) % PHOTOS.length))
        }, 5000);
        return () => {
            clearInterval(interval);
        }
    }, [index]); 
    const classes = useStyles();
    return (
        <div>
            <div className='' style={{position: 'relative'}}>
                <div className={classes.imageContainer}>
                    <img src={PHOTOS[index]} alt='Bungalows' className={classes.image} />
                </div>
                <QuoteBox />
            </div>
        </div>
    );
}

export default Gallery;
