function SearchHolidays(props){
    const [isLoading, setLoading] = useState(true);
    // const [holidayList, setHolidayList] = useState([]);
    useEffect(() => {
        window.scrollTo(0, 0);
        setTimeout(() => setLoading(false), 2000);
    }, []);


    const { query, sort } = QueryString.parse(props.location.search);
    useEffect(() => {
        setLoading(true);
        setTimeout(() => setLoading(false), 2000);
    }, [query]);

    useEffect(() => {
        setLoading(true);
        setTimeout(() => setLoading(false), 2000);
    }, [sort])
    return (
        <div style={{marginTop: 80}}>
            <Header />
            <div className='container'>
            <Grid container spacing={2} className='f-g-1'>
                <Grid item xs={12} md={12} lg={3}>
                    <FilterMenu isLoading={isLoading} />
                </Grid>
                <Grid item xs={12} md={12} lg={9}>
                    <HotelItemList itemList={HOLIDAYS} isLoading={isLoading} />
                </Grid>
            </Grid>
            </div>
        </div>
    );
}


export default SearchHolidays;
