import React, { useState, useEffect } from 'react';
import { useLocation, Redirect } from 'react-router-dom';
import AutoComplete from '../../../../../components/input/autocomplete';
import DateInput from '../../../../../components/input/dateInput';
import SliderInput from '../../../../../components/input/sliderInput';
import CheckboxGroup from '../../../../../components/input/checkboxGroup';
import Text from '../../../../../components/text';


const queryString = require('query-string');


function HotelForm(props){
    const location = useLocation();
    const queryStr = queryString.parse(location.search);
    const query = queryStr.query ? JSON.parse(queryStr.query) : {};

    const [prices, setPrices] = useState([1000, 4000]);

    const [url, setUrl] = useState('');
    const [isRedirect, setRedirect] = useState(false); 


    const onChange = (name, value, sub_name) => {
        if (name && (value || typeof value === 'boolean')) {
            let newQuery = Object.assign({...query});
            if (name === 'price') {
                newQuery.price = value;
            } else if (name === 'property_type') {
                if (!newQuery.property_type) {
                    newQuery.property_type = {};
                }
                newQuery.property_type[sub_name] = value;
            } else {
                newQuery[name] = value;
            }
            setUrl(`/search/hotels?query=${JSON.stringify(newQuery)}`);
            setRedirect(true);
        }        
    }

    useEffect(() => {
        if (isRedirect){
            setRedirect(false);
        }        
    }, [isRedirect]);

    if (isRedirect && url) {
        console.log('Redirecting...\n');
        return(
            <Redirect to={url} />
        );
    }
    console.log(query);
    const propertyType = query.property_type ? query.property_type : {};
    return (
        <div>
            <div className='p-tb-12'>
                <AutoComplete 
                    label='Destinaton or Hotel'
                    name='to'
                    value={query.destination}
                    onChange={(event, newValue) => onChange('destination', newValue)}
                />
            </div>
            <div className='p-tb-12'>
                <DateInput 
                    label='Departure date'
                    onChange={(date)  => onChange('check_in', date)}
                    value={query.check_in}
                />
            </div>
            <div className='p-tb-12'>
                <DateInput 
                    label='Return date'
                    onChange={(date) => onChange('check_out', date)}
                    value={query.check_out}
                />
            </div>
            <div className='p-tb-12'>
                <Text text='Preferences' variant='sideheading' />
            </div>
            <div className='p-tb-12'>
                <SliderInput
                    label='Price per night'
                    name='price'
                    value={prices}
                    onChange={(event, value) =>{onChange('price', value); setPrices(value)}}
                    min={1000}
                    max={4000}
                />
            </div>
            <div className='p-tb-12'>
                <CheckboxGroup
                    label='Property Type'
                    state={[
                        {
                            name: 'hotel',
                            checked: propertyType.hotel,
                            label: 'Hotel'
                        },
                        {
                            name: 'resort',
                            checked: propertyType.resort,
                            label: 'Resort'
                        },
                        {
                            name: 'villa',
                            checked: propertyType.villa,
                            label: 'Villas'
                        },
                        {
                            name: 'home_stay',
                            checked: propertyType.home_stay,
                            label: 'Home Stay'
                        },
                        {
                            name: 'apartment',
                            checked: propertyType.apartment,
                            label: 'Apartment'
                        },
                        {
                            name: 'house_boat',
                            checked: propertyType.house_boat,
                            label: 'House Boat'
                        }
                    ]}
                    onChange={(event) =>onChange('property_type', event.target.checked, event.target.name)}
                />
            </div>
        </div>
    );
}


export default HotelForm;
