import React from 'react';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import useScreen from '../../../../../hooks/useScreen';
import Facilities from './facilities';
import Option from './option';


const useStyles = makeStyles((theme) => ({
    item: {
        padding: theme.spacing(2),
        '&:not(:last-child)': {
            borderBottom: '1px solid rgba(0, 0, 0, 0.08)',
        },
        [theme.breakpoints.down('xs')]: {
            padding: 0,
            borderBottom: 'none',
        },
        
    },
    photoContainer: {
        width: 280,
        height: 'auto',
        [theme.breakpoints.down('sm')]: {
            width: 220,
            height: 'auto',
        },
        [theme.breakpoints.down('xs')]: {
            width: '100%',
            height: 'auto',
            marginBottom: theme.spacing(1),
        }
    },
    photo: {
        width: '100%',
        maxWidth: '100%',
        minWidth: '100%',
        borderRadius: 2,
        boxShadow: theme.shadows[2],
        overflow: 'hidden',
    },
    content: {
        paddingLeft: theme.spacing(1.5),
        width: 'calc(100% - 280px)',
        [theme.breakpoints.down('sm')]: {
            width: 'calc(100% - 220px)',
        },
        [theme.breakpoints.down('xs')]: {
            width: '100%',
            paddingLeft: 0,
        }
    }
}));


function RoomItem (props){
    const { room } = props;
    const classes = useStyles();
    const { screenWidth } = useScreen();
    return (
        <div className={classes.item}>
            <div>
                <div className='r-f-w'>
                    <div className={classes.photoContainer}>
                        <div className={classes.photo}>
                            <img src={room.photos[0]} alt={room.title} />
                        </div>
                    </div>
                    <div className={classes.content}>
                        <Facilities room={room} />
                    </div>
                    
                </div>
            </div>
            <div className='m-t-18'>
                <Grid container spacing={screenWidth > 400 ? 2 : 1}>
                    {
                        room.options.map((option, index) => (
                            <Grid item xs={12} md={6} key={index}>
                                <Option option={option} index={index} screenWidth={screenWidth} />                                
                            </Grid>     
                        ))
                    }
                </Grid>
            </div>
        </div>
    );
}

export default RoomItem;
