import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Text from '../../../../../../components/text';


const useStyles = makeStyles((theme) =>({
    container: {
        marginLeft: 24,
        width: 'calc(100% - 24px)',
        borderLeft: `1px solid #cecece`,
        height: 48,
        position: 'relative',
    },
    start: {
        position: 'absolute',
        top: -6,
        left: -6,
        height: 12,
        width: 12,
        borderRadius: 6,
        backgroundColor: '#cecece'
    },
    startContainer: {
        marginTop: 24,
    }
}));


function Break(props){
    const classes = useStyles();
    return (
        <div className={`${classes.container} ${props.start ? classes.startContainer : ''}`}> 
            {
                props.start ?
                    <span className={classes.start} />
                :
                    null
            }
            {
                props.label ?
                    <div className='p-lr-12 r-f a-c h-100'>
                        
                        <Text text={props.label} variant='meta' />
                    </div>
                :
                    null
            }
            
        </div>
    );
}

export default Break;
