import React, { useRef } from 'react';
import Grid from '@material-ui/core/Grid';
import Overview from './overview';
import Itinerary from './itinerary';
import Review from '../../hotel/info/review';
import Policies from '../../hotel/info/policies';
import Recommendation from './recommended';
import Header from '../header';
import Tabbar from '../../flight/info/overview/tabbar';


function Info(props){
    const overviewRef = useRef();
    const itineraryRef = useRef();
    const policyRef = useRef();
    const reviewRef = useRef();
    const recommendRef = useRef();
    return (
        <React.Fragment>
            <Header 
                title='Pattaya & Bangkok - Weekend Break'
                info='Holiday'
                tabbar={
                    <Tabbar 
                        tabs={['Overview', 'Itinerary', 'Policies', 'Reviews', 'Similar Packages']} 
                        references={[overviewRef, itineraryRef, policyRef, reviewRef, recommendRef]}
                    />
                }
            />
            <div className='container' style={{marginTop: 124}}>
                <div ref={overviewRef}>
                    <Overview references={[overviewRef, itineraryRef, policyRef, reviewRef, recommendRef]} />
                </div>
                <div ref={itineraryRef}>
                    <Grid container spacing={2}>
                        <Grid item xs={12} md={12} lg={8}>
                            <Itinerary />
                        </Grid>
                    </Grid>                    
                </div>
                <div ref={policyRef}>
                    <Policies />
                </div>
                <div ref={reviewRef}>
                    <Review />
                </div>
                <div ref={recommendRef}>
                    <Recommendation />
                </div>
            </div>
        </React.Fragment>
        
    );
}


export default Info;