import React, { useState } from 'react';
import AppBar from '@material-ui/core/AppBar';
import { makeStyles } from '@material-ui/core/styles';
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import AccountCircleRoundedIcon from '@material-ui/icons/AccountCircleRounded';
import HomeRoundedIcon from '@material-ui/icons/HomeRounded';
import ShopRoundedIcon from '@material-ui/icons/ShopRounded';

const useStyles = makeStyles((theme) => ({
    appBar: {
        top: 'auto',
        bottom: 0,
    },
    label: {
        fontSize: 12,
    }
}));


function BottomBar(props){
    const classes = useStyles();
    const [value, setValue] = useState(0);
    console.log(value);
    return (
        <AppBar color='inherit' className={classes.appBar}>
            <BottomNavigation
                value={0}
                onChange={(event, newValue) => {
                    setValue(newValue);
                }}
                showLabels
                className={classes.root}
                >
                <BottomNavigationAction classes={{label: classes.label}} label="Home" icon={<HomeRoundedIcon fontSize='small' />} />
                <BottomNavigationAction label="Shop" icon={<ShopRoundedIcon fontSize='small' />} />
                <BottomNavigationAction label="Account" icon={<AccountCircleRoundedIcon fontSize='small' />} />
            </BottomNavigation>
        </AppBar>
    );
}

export default BottomBar;
