import React from 'react';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import useScreen from '../../../../hooks/useScreen';
import Text from '../../../../components/text';


const useStyles = makeStyles((theme) => ({
    item: {
        paddingTop: theme.spacing(2),
        paddingBottom: theme.spacing(2),
        paddingLeft: theme.spacing(1.5),
        paddingRight: theme.spacing(1.5),
        borderBottom: `1px solid #cecece`,
        borderLeft: `1px solid #cecece`,
        borderRight: `1px solid #cecece`,
        '&:first-child': {
            borderTopLeftRadius: 2,
            borderTopRightRadius: 2,
            borderTop: `1px solid #cecece`,
        },
        '&:last-child': {
            borderBottomLeftRadius: 2,
            borderBottomRightRadius: 2,
        }
    },
    row: {
        borderBottom: `1px solid #cecece`,
        '&:first-child': {
            borderTopLeftRadius: 2,
            borderTopRightRadius: 2,
            borderTop: `1px solid #cecece`,
        },
        '&:last-child': {
            borderBottomLeftRadius: 2,
            borderBottomRightRadius: 2,
        }
    },
    box: {
        // paddingTop: theme.spacing(1),
        // paddingBottom: theme.spacing(1),
        height: 60,
        padding: theme.spacing(1),
        borderRight: `1px solid #cecece`,
        display: 'flex',
        alignItems:'center',
        justifyContent: 'center',
        [theme.breakpoints.down('xs')]: {
            height: 90,
        }
    },
    firstBox: {
        borderLeft: `1px solid #cecece`,
    },
    miniBox: {
        border: '1px solid #cecece',
        borderRadius: 2,
        padding: theme.spacing(1.5),
        '&:not(:last-child)': {
            marginBottom: theme.spacing(1.5),
        },
        '&:first-child': {
            marginTop: theme.spacing(1.5),
        }
    }
}));


const INFO = [
    {
        date: '19th Aug 2020, 5:45 AM',
        departure: 'New Delhi',
        arrival: 'Agartala',
        time: '02 Hrs 45 Mins',
        cost: ' 10,58,750',
        empty: 'Empty Leg',
    },
    {
        date: '19th Aug 2020, 9:30 AM',
        departure: 'Agartala',
        arrival: 'Allahabad',
        time: '01 Hrs 45 Mins',
        cost: ' 6,73,750',
        empty: 'With Pax',
    },
    {
        date: '19th Aug 2020, 12:15 PM',
        departure: 'Allahabad',
        arrival: 'New Delhi',
        time: '01 Hrs 25 Mins',
        cost: ' 5,45,417',
        empty: 'Empty Leg',
    },
    {
        date: '28th Aug 2020, 4:05 PM',
        departure: 'New Delhi',
        arrival: 'Allahabad',
        time: '01 Hrs 25 Mins',
        cost: ' 5,45,417',
        empty: 'Empty Leg',
    },
    {
        date: '28th Aug 2020, 6:30 PM',
        departure: 'Allahabad',
        arrival: 'Agartala',
        time: '01 Hrs 45 Mins',
        cost: ' 6,73,750',
        empty: 'With Pax',
    },
    {
        date: '28th Aug 2020, 9:15 PM',
        departure: 'Agartala',
        arrival: 'New Delhi',
        time: '02 Hrs 45 Mins',
        cost: ' 10,58,750',
        empty: 'Empty Leg',
    }
]


function PriceInfo(props){
    const classes = useStyles();
    const { screenWidth } = useScreen();
    return(
        <div className='m-t-18'>
            <div className=''>
                <Text text='Cost Details' variant='sideheading' />
            </div>
            <div className='m-t-12'>
                <div className='r-f a-c'>
                    <Text text='Aircraft: Falcon 2000' variant='text' />
                    <div className='f-g-1' />
                    <Text text='Rate per hour:  3,85,000' variant='text' />
                </div>
            </div>
            {
                screenWidth < 960 ?
                    <div>
                        {
                            INFO.map((info, index) => (
                                <div className={classes.miniBox}>
                                    <div className='r-f a-c'>
                                        <Text text={`Phase ${index}`} variant='title' />
                                    </div>
                                    <div className='m-t-12'>
                                        <div className='r-f a-c'>
                                            <div>
                                                <Text text='Date' variant='title' />
                                            </div>
                                            <div className='f-g-1' />
                                            <div>
                                                <Text text={info.date} variant='text' />
                                            </div>
                                        </div>
                                    </div>
                                    <div className='m-t-8'>
                                        <div className='r-f a-c'>
                                            <div>
                                                <Text text='Departure' variant='title' />
                                            </div>
                                            <div className='f-g-1' />
                                            <div>
                                                <Text text={info.departure} variant='text' />
                                            </div>
                                        </div>
                                    </div>
                                    <div className='m-t-8'>
                                        <div className='r-f a-c'>
                                            <div>
                                                <Text text='Arrival' variant='title' />
                                            </div>
                                            <div className='f-g-1' />
                                            <div>
                                                <Text text={info.arrival} variant='text' />
                                            </div>
                                        </div>
                                    </div>
                                    <div className='m-t-8'>
                                        <div className='r-f a-c'>
                                            <div>
                                                <Text text='Flight Time' variant='title' />
                                            </div>
                                            <div className='f-g-1' />
                                            <div>
                                                <Text text={info.time} variant='text' />
                                            </div>
                                        </div>
                                    </div>
                                    <div className='m-t-8'>
                                        <div className='r-f a-c'>
                                            <div>
                                                <Text text='Cost' variant='title' />
                                            </div>
                                            <div className='f-g-1' />
                                            <div>
                                                <Text text={info.cost} variant='text' />
                                            </div>
                                        </div>
                                    </div>
                                    <div className='m-t-8'>
                                        <div className='r-f a-c'>
                                            {/* <div>
                                                <Text text='Departure' variant='title' />
                                            </div> */}
                                            <div className='f-g-1' />
                                            <div>
                                                <Text text={info.empty} variant='text' />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            ))
                        }
                    </div>
                   
                :
            
            <div className='m-t-12'>
                <div className={classes.row}>
                    <Grid container>
                        <Grid item xs={2}>
                            <div className={`${classes.box} ${classes.firstBox}`}>
                                <Text text='Date' variant='title' textAlign='center' />
                            </div>
                        </Grid>
                        <Grid item xs={2}>
                            <div className={classes.box}>
                                <Text text='Departure' variant='title' textAlign='center' />
                            </div>
                        </Grid>
                        <Grid item xs={2}>
                            <div className={classes.box}>
                                <Text text='Arrival' variant='title' textAlign='center' />
                            </div>
                        </Grid>
                        <Grid item xs={2}>
                            <div className={classes.box}>
                                <Text text='Flight time' variant='title' textAlign='center' />
                            </div>
                        </Grid>
                        <Grid item xs={2}>
                            <div className={classes.box}>
                                <Text text='Cost' variant='title' textAlign='center' />
                            </div>
                        </Grid>
                        <Grid item xs={2}>
                            <div className={`${classes.box} h-100`}/>
                        </Grid>
                    </Grid>
                </div>
                {
                    INFO.map((info, index) => (
                        <div className={classes.row} key={index}>
                            <Grid container>
                                <Grid item xs={2}>
                                    <div className={`${classes.box} ${classes.firstBox}`}>
                                        <Text text={info.date} variant='text' textAlign='center' />
                                    </div>
                                </Grid>
                                <Grid item xs={2}>
                                    <div className={classes.box}>
                                        <Text text={info.departure} variant='text' textAlign='center' />
                                    </div>
                                </Grid>
                                <Grid item xs={2}>
                                    <div className={classes.box}>
                                        <Text text={info.arrival} variant='text' textAlign='center' />
                                    </div>
                                </Grid>
                                <Grid item xs={2}>
                                    <div className={classes.box}>
                                        <Text text={info.time} variant='text' textAlign='center' />
                                    </div>
                                </Grid>
                                <Grid item xs={2}>
                                    <div className={classes.box}>
                                        <Text text={info.cost} variant='text' textAlign='center' />
                                    </div>
                                </Grid>
                                <Grid item xs={2}>
                                    <div className={classes.box}>
                                        <Text text={info.empty} variant='text' textAlign='center' />
                                    </div>
                                </Grid>
                            </Grid>
                        </div>
                    ))
                }
            </div>
            }
        </div>
    );
}


export default PriceInfo