import React from 'react';
import Chip from '../../../../components/chip';
import Text from '../../../../components/text';
import ReviewItem from './ReviewItem';


const REVIEWS = [
    {
        title: 'Overall good',
        text: 'The hotel took every possible steps to make our safety possible',
        user: 'John Doe',
        timestamp: 'Jul 18, 2020',
        rating: 4
    },
    {
        title: 'Nice stay',
        text: 'Beautiful Resort, staff friendly, good food.',
        user: 'Jake Doe',
        timestamp: 'Mar 26, 2020',
        rating: 5
    },
    {
        title: 'Good to worst',
        text: 'Our first 3days of stay was good and was impressed with ambience and service. And then got a call from management saying they upgraded our room as a compliment it was all leaking and filled with cockroaches and the room was stinking as we spent most of our time exploring Goa, we return back tired at night and was not in a mood to ask for a change of room Apart from that, it was a good place to visit with family with all amenities served.',
        user: 'Mar 16, 2020',
        timestamp: 'Jul 18, 2020',
        rating: 2
    }
];


function Review(props){
    return (
        <div className='m-t-18' style={{marginTop: 64}}>
            <div>
                <Text variant='sideheading' text='Verified Ratings & Reviews' />
                <Text variant='meta' text='Take a look at verified ratings & reviews posted by guests' />
            </div>
            <div className='m-t-12'>
                <div className='r-f a-c'>
                    <div>
                        <Chip label='4.2/5' color='success' size='medium' />
                        <div className='m-t-6'>
                            <Text variant='meta' text='1024 Reviews' textAlign='center' />
                        </div>                        
                    </div>
                    <div className='m-l-12 p-lr-12'>
                        <div>
                            <Text variant='title' text='3.9' textAlign='center' />
                        </div>
                        <div>
                            <Text variant='meta' text='Child Friendlyness' textAlign='center' />
                        </div>
                    </div>
                    <div className='m-l-12 p-lr-12'>
                        <div>
                            <Text variant='title' text='4.5' textAlign='center' />
                        </div>
                        <div>
                            <Text variant='meta' text='Location' textAlign='center' />
                        </div>
                    </div>
                    <div className='m-l-12 p-lr-12'>
                        <div>
                            <Text variant='title' text='4.3' textAlign='center' />
                        </div>
                        <div>
                            <Text variant='meta' text='Hospitality' textAlign='center' /> 
                        </div>
                    </div>
                </div>
            </div>
            <div className='m-t-12'>
                {
                    REVIEWS.map((review, index) => (
                        <ReviewItem review={review} key={index} />
                    ))
                }
            </div>
        </div>
    );
}


export default Review;